<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-button @click="addDialogVisible=true"  type="primary" v-if="sysFlag">添加业务员</el-button>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" style="width: 100%" v-loading="loading">
				<el-table-column prop="name" label="姓名">
				</el-table-column>
				<el-table-column prop="phone" label="电话">
				</el-table-column>
			</el-table>
		</el-col>

		<el-dialog title="添加业务员" v-model="addDialogVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="姓名" :rules="{required: true}">
					<el-input v-model="name"></el-input>
				</el-form-item>
				<el-form-item label="电话(登录名)" :rules="{required: true}">
					<el-input v-model="phone"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addDialogVisible = false">取消</el-button>
				<el-button type="primary" @click="addSaleUser" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				list: [],
				name:"",
				phone:"",
				addDialogVisible:false,
				addLoading:false,
				sysFlag:false,
				password:""
			}
		},
		methods: {
			addSaleUser(){
				if(!this.name){
					baseUtile.tip("请输入业务员姓名");
					return;
				}
				if(!this.phone){
					baseUtile.tip("请输入业务员电话");
					return;
				}
				var params = {
					name:this.name,
					phone:this.phone,
				}
				this.addLoading = true;
				baseUtile.httpSend("/addSaleUser.html",params,data=>{
					baseUtile.tip("添加成功!","success");
					this.addDialogVisible = false;
					this.addLoading = false;
					this.getAllSaleUser();
				})
			},
			getAllSaleUser(){
				baseUtile.httpSend("/getAllSaleUser.html",null,data=>{
					this.list = data.saleUser;
				})
			}
		},
		mounted() {
			this.getAllSaleUser();
			var user = JSON.parse(sessionStorage.getItem('user'));
			if(user.username == "admin") {
				this.sysFlag = true;
			}
		}
	}
</script>

<style>

</style>


