<template>
	<div style="height: 100%;width: 100%;background-color: white;">
		<el-row>
			<el-col :span="24" style="height: 50px;">
				<img src="../assets/logo.png" style="height: 35px;margin-left: 150px;margin-top: 5px;" />
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24" style="position: relative;">
				<div class="login-bg">
					<el-form :inline="true" :rules="rules2" ref="ruleForm2">
						<el-form-item>
							<label style="font-size: 20px;margin-top: 15px;">用户登录</label>
						</el-form-item>
						<el-form-item>
							<el-input type="text" placeholder="输入用户名" v-model="ruleForm2.username">
								<template slot="prepend"><img src="../assets/yhm.png" /></template>
							</el-input>
						</el-form-item>
						<el-form-item>
							<el-input type="password" placeholder="输入密码" v-model="ruleForm2.password" @keyup.enter.native="handleSubmit2">
								<template slot="prepend"><img src="../assets/mima.png" /></template>
							</el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" style="width:240px;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
						</el-form-item>
					</el-form>
				</div>
				<img src="../assets/bj.png" style="width: 100%;" />
			</el-col>
		</el-row>
		<el-row style="text-align: center;margin-top: 20px;">
			<div style="width: 100%;">
				<el-col :span="6" style="position:relative;">
					<img class="logo-img" src="../assets/icon1.png" />
					<span class="logo-word">生活缴费</span>
				</el-col>
				<el-col :span="6" style="position:relative;">
					<img class="logo-img" src="../assets/icon2.png" />
					<span class="logo-word">小区公告</span>
				</el-col>
				<el-col :span="6" style="position:relative;">
					<img class="logo-img" src="../assets/icon3.png" />
					<span class="logo-word">报事报修</span>
				</el-col>
				<el-col :span="6" style="position:relative;">
					<img class="logo-img" src="../assets/icon4.png" />
					<span class="logo-word">财务分析</span>
				</el-col>
			</div>
		</el-row>
		<el-row style="text-align: center;color: gray;padding: 15px;background-color: white;">
			<el-col :span="24" style="margin-top: 150px;">
				Copyright © 在线物业(ZXWY.COM)Inc.All Rights Reserved.
				<!--Copyright © 杭州早早科技有限公司 (HZZZKJ.COM)Inc.All Rights Reserved.
				浙ICP备17012871号-2-->
			</el-col>
		</el-row>
	</div>

</template>
<script>
	import {
		requestLogin
	} from '../api/api';
	import {
		baseUtile
	} from '../api/baseUtile';

	//import NProgress from 'nprogress'
	export default {
		data() {
			return {
				logining: false,
				ruleForm2: {
					username: '',
					password: ''
				},
				rules2: {
					username: [{
							required: true,
							message: '请输入账号',
							trigger: 'blur'
						},
						//{ validator: validaePass }
					],
					password: [{
							required: true,
							message: '请输入密码',
							trigger: 'blur'
						},
						//{ validator: validaePass2 }
					]
				},
				checked: true
			};
		},
		methods: {
			handleSubmit2(ev) {
				var _this = this;
//				sessionStorage.setItem('user', JSON.stringify(_this.ruleForm2));
//				_this.$router.push({
//					path: '/home'
//				});
				if(!this.ruleForm2.username) {
					baseUtile.tip("请输入用户名!");
					return;
				}
				if(!this.ruleForm2.password) {
					baseUtile.tip("请输入密码!");
					return;
				}
				this.logining = true;
				var loginParams = {
					username: this.ruleForm2.username,
					password: this.ruleForm2.password
				};
				baseUtile.httpSend("/login.html", loginParams, function(data) {
					_this.logining = false;
					if(!!data.r) {
						baseUtile.tip("登陆成功", "success");
						_this.ruleForm2.isSaler = data.isSaler;
						sessionStorage.setItem('user', JSON.stringify(_this.ruleForm2));
						_this.$router.push({
							path: '/home'
						});
					} else {
						baseUtile.tip(data.e, "error");
						_this.logining = false;

					}
				})
			}
		},
		mounted() {
			var doms = document.getElementsByClassName("el-input-group__prepend");
			for(var i = 0; i < doms.length; i++) {
				doms[i].style.width = 30 + "px";
			}
		}
	}
</script>
<style lang="scss" scoped>
	.login-container {
		/*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
		-webkit-border-radius: 5px;
		border-radius: 5px;
		-moz-border-radius: 5px;
		background-clip: padding-box;
		margin: 180px auto;
		width: 350px;
		padding: 35px 35px 15px 35px;
		background: #fff;
		border: 1px solid #eaeaea;
		box-shadow: 0 0 25px #cac6c6;
		.title {
			margin: 0px auto 40px auto;
			text-align: center;
			color: #505458;
		}
		.remember {
			margin: 0px 0px 35px 0px;
		}
	}
	
	.logo-word {
		font-size: 18px;
		margin-left: 15px;
		margin-bottom: 2px;
		top: 5px;
		position: absolute;
		color: #595757;
	}
	
	.login-bg {
		background: url(../assets/beijing.png) no-repeat;
		background-size: 100% 100%;
		position: absolute;
		z-index: 10;
		vertical-align: middle;
		width: 250px;
		height: 250px;
		text-align: center;
		color: #20A0FF;
		padding: 15px;
		margin: auto;
		position: absolute;
		top: 0;
		left: 600px;
		bottom: 0;
		right: 0;
	}
	
	.el-input-group__prepend {
		padding-left: 0px;
		padding-right: 0px;
	}
	
	.logo-img {
		width: 26px;
		height: 26px;
	}
	
	.el-form-item {
		margin-right: 0;
	}
</style>


