<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-select placeholder="小区" v-model="housingId" :filterable=true>
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="queryHousingWy()">查询</el-button>
						<!--<el-button type="primary" @click="addDialogVisible=true">添加</el-button>-->
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" border style="width: 100%" v-loading="loading">
				<el-table-column prop="departmentName" label="物业名称">
				</el-table-column>
				<el-table-column prop="name" label="小区名称">
				</el-table-column>
				<el-table-column prop="community_id" label="支付宝编号">
				</el-table-column>
				<el-table-column prop="address" label="详细地址">
				</el-table-column>
				<el-table-column prop="community_address" label="街道地址">
				</el-table-column>
				<el-table-column prop="contractPeople" label="联系人">
				</el-table-column>
				<el-table-column prop="phone" label="联系电话">
				</el-table-column>
				<el-table-column prop="token" label="token">
				</el-table-column>
				<el-table-column label="操作">
					<template scope="scope">
						<el-button type="text" size="small" @click="openEditModel(scope.row)" v-if="sysFlag">修改</el-button>
						<el-button type="text" size="small" @click="openChangeDepartmentModel(scope.row)" v-if="sysFlag">修改所属物业</el-button>
						<el-button type="text" size="small" @click="deleteBillAccountFromAlipay(scope.row.id)">删除账单</el-button>
						<el-button type="text" size="small" @click="deleteHousing(scope.row.id)" v-show="scope.row.community_id==''">删除小区</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="queryHousingWy" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="小区收录" v-model="addDialogVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区名称" :rules="{required: true}">
					<el-input v-model="name"></el-input>
				</el-form-item>
				<el-form-item label="物业" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="department" :filterable=true>
						<el-option v-for="item in dapartmentList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="详细地址" :rules="{required: true}">
					<el-input v-model="address"></el-input>
				</el-form-item>
				<el-form-item label="地址" :rules="{required: true}">
					<el-input v-model="community_address"></el-input>
				</el-form-item>
				<el-form-item label="省份编码" :rules="{required: true}">
					<el-input v-model="province_code" value="500000"></el-input>
				</el-form-item>
				<el-form-item label="地级市编码" :rules="{required: true}">
					<el-input v-model="city_code" value="500100"></el-input>
				</el-form-item>
				<el-form-item label="区县编码" :rules="{required: true}">
					<el-input v-model="district_code" value="500104"></el-input>
				</el-form-item>
				<el-form-item label="区所在的经纬度" :rules="{required: true}">
					<el-input v-model="community_locations" value="[106.481391|29.465131]"></el-input>
				</el-form-item>
				<el-form-item label="联系人">
					<el-input v-model="contractPeople"></el-input>
				</el-form-item>
				<el-form-item label="联系电话" :rules="{required: true}">
					<el-input v-model="phone"></el-input>
				</el-form-item>
				<el-form-item label="支付账户">
					<el-input v-model="payAccount"></el-input>
				</el-form-item>
				<el-form-item label="商户PID">
					<el-input v-model="pid"></el-input>
				</el-form-item>
				<el-form-item label="token" :rules="{required: true}">
					<el-input v-model="token"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addDialogVisible = false">取消</el-button>
				<el-button type="primary" @click.native="saveHousing" :loading="saveHousingLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="小区修改" v-model="editDialogVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区名称" :rules="{required: true}">
					<el-input v-model="edit.name"></el-input>
				</el-form-item>
				<el-form-item label="业务员" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="edit.saleId" :filterable=true>
						<el-option v-for="item in saleUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="详细地址" :rules="{required: true}">
					<el-input v-model="edit.address"></el-input>
				</el-form-item>
				<el-form-item label="地址" :rules="{required: true}">
					<el-input v-model="edit.community_address"></el-input>
				</el-form-item>
				<el-form-item label="省份编码" :rules="{required: true}">
					<el-input v-model="edit.province_code"></el-input>
				</el-form-item>
				<el-form-item label="地级市编码" :rules="{required: true}">
					<el-input v-model="edit.city_code"></el-input>
				</el-form-item>
				<el-form-item label="区县编码" :rules="{required: true}">
					<el-input v-model="edit.district_code"></el-input>
				</el-form-item>
				<el-form-item label="经纬度" :rules="{required: true}">
					<el-input v-model="edit.community_locations"></el-input>
				</el-form-item>

				<el-form-item label="联系电话" :rules="{required: true}">
					<el-input v-model="edit.phone"></el-input>
				</el-form-item>
				<el-form-item label="支付账户">
					<el-input v-model="edit.payAccount"></el-input>
				</el-form-item>
				<el-form-item label="商户PID">
					<el-input v-model="edit.pid"></el-input>
				</el-form-item>
				<el-form-item label="token" :rules="{required: true}">
					<el-input v-model="edit.token"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editDialogVisible = false">取消</el-button>
				<el-button type="primary" @click.native="saveUpdateHousing" :loading="editHousingLoading">提交</el-button>
			</div>
		</el-dialog>
		
		<el-dialog title="小区物业修改" v-model="editDepartmentVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区名称" :rules="{required: true}">
					<el-input v-model="edit.name" readonly="readonly"></el-input>
				</el-form-item>
				<el-form-item label="物业" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="edit.departmentId" :filterable=true>
						<el-option v-for="item in dapartmentList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editDepartmentVisible = false">取消</el-button>
				<el-button type="primary" @click.native="changeDepartment" :loading="editHousingLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				dapartmentList: [],
				list: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				addDialogVisible: false,
				areaName: "",
				name: "",
				department: "",
				address: "",
				community_address: "",
				province_code: "",
				city_code: "",
				district_code: "",
				community_locations: "",
				contractPeople: "",
				phone: "",
				payAccount: "",
				pid: "",
				token: "",
				saveHousingLoading: false,
				edit: {},
				editDialogVisible: false,
				editHousingLoading: false,
				housingId: "",
				sysFlag: false,
				editDepartmentVisible:false,
				saleUserList:[]
			}
		},
		methods: {
			getAllSaleUser(){
				baseUtile.httpSend("/getAllSaleUser.html",null,data=>{
					this.saleUserList = data.saleUser;
				})
			},
			changeDepartment(){
				var housingId = this.edit.id;
				var department;
				this.dapartmentList.forEach(item=> {
					if(item.id==this.edit.departmentId)
						department = item;
					}
				)
				var params = {
					housingId:housingId,
					departmentId:department.id,
					departmentCode:department.code
				}
				baseUtile.httpSend("/changeDepartment.html",params,data=>{
					baseUtile.tip(data.msg);
					this.editDepartmentVisible = false;
					this.queryHousing();
				})
			},
			openChangeDepartmentModel(row){
				this.editDepartmentVisible = true;
				this.edit = JSON.parse(JSON.stringify(row));
			},
			deleteHousing(housingId) {
				baseUtile.confirm("删除小区前请确定已经删除该小区下所属楼栋、单元及房屋?", res => {
					if(res) {
						var params = {
							housingId: housingId
						}
						baseUtile.httpSend("/deleteHousing.html", params, data => {
							baseUtile.tip(data.msg, "success");
							this.queryHousingWy();
						})
					}
				})
			},
			queryHousing: function() {
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					this.houseList = data.list;
					this.$forceUpdate();
				})
			},
			queryHousingWy: function(currPage) {
				if(!!currPage) {
					this.currPage = currPage;
				}
				var that = this;
				that.loading = true;
				var params = {
					currPage: that.currPage,
					name: that.areaName,
					housingId: that.housingId
				}
				baseUtile.httpSend("/queryHousingWy.html", params, data => {
					that.list = data.list;
					that.loading = false;
					that.count = data.count;
				})
			},
			saveHousing: function() {
				var that = this;
				if(!that.name) {
					baseUtile.tip("请输入小区名称");
					return;
				}
				if(!that.department) {
					baseUtile.tip("请选择物业");
					return;
				}
				if(!that.address) {
					baseUtile.tip("请输入详细地址");
					return;
				}
				if(!that.community_address) {
					baseUtile.tip("请输入地址");
					return;
				}
				if(!that.province_code) {
					baseUtile.tip("请输入省份编码");
					return;
				}
				if(!that.city_code) {
					baseUtile.tip("请输入地级市编码");
					return;
				}
				if(!that.district_code) {
					baseUtile.tip("请输入区县编码");
					return;
				}
				if(!that.community_locations) {
					baseUtile.tip("请输入区所在经纬度");
					return;
				}
				if(!that.phone) {
					baseUtile.tip("请输入联系电话");
					return;
				}
				if(!that.token) {
					baseUtile.tip("请输入token");
					return;
				}
				var params = {
					name: that.name,
					department: that.department,
					address: that.address,
					community_address: that.community_address,
					province_code: that.province_code,
					city_code: that.city_code,
					district_code: that.district_code,
					community_locations: that.community_locations,
					contractPeople: that.contractPeople,
					phone: that.phone,
					payAccount: that.payAccount,
					pid: that.pid,
					token: that.token,
				}
				that.saveHousingLoading = true;
				baseUtile.httpSend("/saveHousing.html", params, data => {
					baseUtile.tip(data.msg);
					that.addDialogVisible = false;
					that.saveHousingLoading = false;
					that.queryHousingWy();
				})
			},
			getDepartment_menu: function() {
				var that = this;
				var params = {
					currPage: "",
					type: "none"
				}
				baseUtile.httpSend("/department_menu.do", params, function(data) {
					that.dapartmentList = data.list;
				})
			},
			deleteBillAccountFromAlipay: function(houseingId) {
				var that = this;
				var params = {
					houseingId: houseingId
				}
				baseUtile.confirm("确定删除么?", res => {
					if(res) {
						baseUtile.httpSend("/deleteBillAccountFromAlipay.html", params, data => {
							baseUtile.tip(data.msg);
							that.getDepartment_menu();
						})
					}
				})

			},
			openEditModel: function(row) {
				this.editHousingLoading = false;
				this.editDialogVisible = true;
				this.edit = JSON.parse(JSON.stringify(row));
				console.log(this.edit)
			},
			saveUpdateHousing: function() {
				var that = this;
				if(!this.edit.name) {
					baseUtile.tip("请输入小区名称");
					return;
				}
				if(!this.edit.address) {
					baseUtile.tip("请输入详细地址");
					return;
				}
				if(!this.edit.community_address) {
					baseUtile.tip("请输入地址");
					return;
				}
				if(!this.edit.province_code) {
					baseUtile.tip("请输入省份编码");
					return;
				}
				if(!this.edit.city_code) {
					baseUtile.tip("请输入地级市编码");
					return;
				}
				if(!this.edit.district_code) {
					baseUtile.tip("请输入区县编码");
					return;
				}
				if(!this.edit.community_locations) {
					baseUtile.tip("请输入区所在经纬度");
					return;
				}
				if(!this.edit.phone) {
					baseUtile.tip("请输入联系电话");
					return;
				}
				if(!this.edit.token) {
					baseUtile.tip("请输入token");
					return;
				}
				var params = {
					name: this.edit.name,
//					department: this.edit.department,
					address: this.edit.address,
					community_address: this.edit.community_address,
					province_code: this.edit.province_code,
					city_code: this.edit.city_code,
					district_code: this.edit.district_code,
					community_locations: this.edit.community_locations,
					contractPeople: this.edit.contractPeople,
					phone: this.edit.phone,
					payAccount: this.edit.payAccount,
					pid: this.edit.pid,
					token: this.edit.token,
					saleId:this.edit.saleId,
					id: this.edit.id
				}
				that.editHousingLoading = true;
				baseUtile.httpSend("/saveUpdateHousing.html", params, data => {
					baseUtile.tip(data.msg);
					that.editDialogVisible = false;
					that.editHousingLoading = false;
					that.queryHousingWy();
				})
			}
		},
		mounted() {
			this.queryHousing();
			var user = JSON.parse(sessionStorage.getItem('user'));
			if(!user.isSaler) {
				this.sysFlag = true;
			}
			this.getDepartment_menu();
			this.queryHousingWy();
			this.getAllSaleUser();
		}
	}
</script>


