import axios from 'axios';
import { MessageBox } from 'element-ui';
import { Message } from 'element-ui';
import { Loading } from 'element-ui';
var SERVER_IP = "";
var SERVER_PRE_URL = SERVER_IP + "";///lj_estate_manage
export const baseUtile = {
    getServerIp: function() {
        return SERVER_IP;
    },
    getServerPreUrl: function() {
        return SERVER_PRE_URL;
    },
    httpSend: function(url, params, callBack) {
        axios.post(SERVER_PRE_URL + url + "", this.params(params))
            .then(function(response) {
                callBack(response.data);
            })
            .catch(function(error) {
                console.log(error);
            });
    },
    params:function(data){
		var s=[];
		for(var key in data){
			var value=data[key]
			if(typeof(value)=='object'){
				for(var o in value){
					s[ s.length ] = encodeURIComponent(key)+ "=" + encodeURIComponent(value[o]);
				}
			}else{
				s[s.length ] = encodeURIComponent(key)+ "=" + encodeURIComponent(value);
			}
		}
		return s= s.toString().replace(/,/g,'&');
	},
    //对话框
    alert: function(msg) {
        MessageBox.alert(msg, '系统提示', {
            confirmButtonText: '确定',
            callback: action => {}
        });
    },
    //消息确认框
    confirm: function(msg, callBack) {
        MessageBox.confirm(msg, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            callBack(true);
        }).catch(() => {
            callBack(false);
        });
    },
    //提示框type:sucess成功提示，null提示,warning警告,error错误
    tip: function(msg, type) {
        if (!type) {
            Message({
                showClose: true,
                message: msg
            });
        } else {
            Message({
                showClose: true,
                message: msg,
                type: type
            });
        }
    },
    dateFormat:function(fmt,date){
		var o = {
			"M+": date.getMonth() + 1, //月份
			"d+": date.getDate(), //日
			"h+": date.getHours(), //小时
			"m+": date.getMinutes(), //分
			"s+": date.getSeconds(), //秒
			"q+": Math.floor((date.getMonth() + 3) / 3), //季度
			"S": date.getMilliseconds() //毫秒
		};
		if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
		for (var k in o)
			if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		return fmt;
	},
}


