<template>
	<section>
		<el-col :span="15">
			<el-form label-width="100px">
				<el-form-item label="用户ID">
					<el-input disabled v-model="user.userId"></el-input>
				</el-form-item>
				<el-form-item label="用户名">
					<el-input disabled v-model="user.username"></el-input>
				</el-form-item>
				<el-form-item label="认证状态">
					<el-input disabled value="未上传" v-if="user.status==0"></el-input>
					<el-input disabled value="待审核" v-if="user.status==1"></el-input>
					<el-input disabled value="已认证" v-if="user.status==2"></el-input>
				</el-form-item>
				<el-form-item label="邮箱">
					<el-input v-model="user.email"></el-input>
				</el-form-item>
				<el-form-item label="QQ">
					<el-input v-model="user.qq"></el-input>
				</el-form-item>
				<el-form-item label="电话">
					<el-input v-model="user.phone"></el-input>
				</el-form-item>
				<el-form-item label="身份证号">
					<el-input v-model="user.certificate"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" style="float:right" @click="editUserInfo">保存</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</section>
</template>

<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				user: {},
				data: {
					moduleid: 0,
					filemodule: "user",
					deleteIds: "-1",
					path: '/newiadpush/user'
				}
			}
		},
		methods: {
			getUserInfo: function() {
				var that = this;
				baseUtile.httpSend("/getUserInfo.html", null, data => {
					that.user = data.sysUser;
				})
			},
			editUserInfo: function() {
				var params = {
					certification: this.user.certification,
					email: this.user.email,
					qq: this.user.qq,
					phone: this.user.phone,
					certificate: this.user.certificate
				}
				baseUtile.httpSend("/personal_info.html", params, data => {
					baseUtile.tip(data.m, "success");
				})
			},
			onSuccess: function() {
				baseUtile.tip("上传成功!","success");
			},
			beforeUpload: function() {
				this.data = {
					moduleid: this.user.userId,
					filemodule: "user",
					deleteIds: "-1",
					path: '/newiadpush/user'
				}
			}
		},
		mounted() {
			this.getUserInfo();
		}
	}
</script>


