<template>
	<section>
		<el-col :span="14">
			<el-form label-width="100px">
				<el-form-item label="小区">
					<el-col :span="24">
						<el-select style="width: 100%;" placeholder="请选择" v-model="housingId" :filterable=true @change="getBuidingsById">
							<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="楼栋">
					<el-col :span="24">
						<el-select style="width: 100%;" placeholder="请选择" v-model="buildingId" :filterable=true @change="getLocationsByBuildingId">
							<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="单元">
					<el-col :span="24">
						<el-select style="width: 100%;" placeholder="请选择" v-model="locationId" :filterable=true @change="quyerRoomInfo">
							<el-option v-for="item in localtionList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="房屋">
					<el-col :span="24">
						<el-select style="width: 100%;" placeholder="请选择" v-model="roomId" :filterable=true @change="queryProprietor">
							<el-option v-for="item in roomList" :key="item.id" :label="item.room" :value="item.id">
							</el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="业主姓名">
					<el-col :span="24">
						<el-select style="width: 100%;" v-model="addOrder.proprietorId" filterable placeholder="请选择">
							<el-option v-for="item in proprietorList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="费用类型">
					<el-col :span="24">
						<el-select style="width: 100%;" v-model="addOrder.cost_type" filterable allow-create placeholder="选择或输入">
							<el-option :label="物业管理费" :key="物业管理费" :value="'物业管理费'">
							</el-option>
							<el-option :label="水电公摊" :key="水电公摊费" :value="'水电公摊费'">
							</el-option>
							<el-option :label="垃圾清运费" :key="垃圾清运费" :value="'垃圾清运费'">
							</el-option>
							<el-option :label="公共照明费" :key="公共照明费" :value="'公共照明费'">
							</el-option>
							<el-option :label="水费" :key="水电公摊费" :value="'水费'">
							</el-option>
							<el-option :label="电费" :key="电费" :value="'电费'">
							</el-option>
							<el-option :label="蒸汽费" :key="蒸汽费" :value="'蒸汽费'">
							</el-option>
							<el-option :label="其它" :key="其它" :value="'其它'">
							</el-option>
						</el-select>
					</el-col>

				</el-form-item>
				<el-form-item label="账期">
					<el-col :span="24">
						<el-col :span="24">
							<el-input v-model="addOrder.acct_period" placeholder="账期yyyyMMdd" ></el-input>
						</el-col>
						<!--<el-date-picker style="width: 100%;" v-model="addOrder.acct_period" type="month" placeholder="选择日期" format="yyyy年MM月">
						</el-date-picker>-->
					</el-col>

				</el-form-item>
				<el-form-item label="出账日期">
					<el-col :span="24">
						<el-date-picker style="width: 100%;" v-model="addOrder.release_day" type="date" placeholder="选择日期" format="yyyyMMdd">
						</el-date-picker>
					</el-col>

				</el-form-item>
				<el-form-item label="缴费截止">
					<el-col :span="24">
						<el-date-picker style="width: 100%;" v-model="addOrder.deadline" type="date" placeholder="选择日期" format="yyyyMMdd">
						</el-date-picker>
					</el-col>

				</el-form-item>
				<el-form-item label="账单金额">
					<el-col :span="24">
						<el-input v-model="addOrder.bill_entry_amount" type="number"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="saveBillAccount" :loading="addLoading" style="float: right;">保存账单</el-button>
				</el-form-item>
			</el-form>
		</el-col>

	</section>
</template>

<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				housingId: "",
				buildingId: "",
				locationId: "",
				roomId: "",
				localtionList: [],
				proprietorList: [],
				addOrder: {
					"proprietorId": "",
					"cost_type": "",
					"acct_period": "",
					"release_day": "",
					"deadline": "",
					"bill_entry_amount": ""
				},
				addLoading: false,
				importModelVisible: false,
				importLoading: false,
				proprietorId: "",
				houseList: [],
				buildList: [],
				localtionList: [],
				roomList: []
			}
		},
		methods: {
			saveBillAccount: function() {
				var that = this;
				if(!that.addOrder.proprietorId) {
					baseUtile.tip("请选择业主!");
					return;
				}
				if(!that.addOrder.cost_type) {
					baseUtile.tip("请选择费用类型!");
					return;
				}
				if(!that.addOrder.acct_period) {
					baseUtile.tip("请选择账期!");
					return;
				}
				if(!that.addOrder.release_day) {
					baseUtile.tip("请选择出账日期!");
					return;
				}
				if(!that.addOrder.deadline) {
					baseUtile.tip("请选择缴费截止日期!");
					return;
				}
				if(!that.addOrder.bill_entry_amount) {
					baseUtile.tip("请输入账单金额!");
					return;
				}
				that.addLoading = true;
				var params = {
					proprietorId: that.addOrder.proprietorId, //that.addOrder.proprietorId
					cost_type: that.addOrder.cost_type,
					acct_period: that.addOrder.acct_period,//baseUtile.dateFormat("yyyyMMdd", that.addOrder.acct_period),
					release_day: baseUtile.dateFormat("yyyyMMdd", that.addOrder.release_day),
					deadline: baseUtile.dateFormat("yyyyMMdd", that.addOrder.deadline),
					bill_entry_amount: that.addOrder.bill_entry_amount
				}
				baseUtile.httpSend("/saveBillAccount.html", params, data => {
					baseUtile.tip("添加成功!", "success");
					that.addLoading = false;
					that.addModelVisible = false;
					that.$router.push({
						path: '/OrderManage'
					});
					that.queryBillAccount();
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			getBuidingsById: function(housingId) {
				var that = this;
				var params = {
					id: housingId
				}
				baseUtile.httpSend("/getBuidingsById.html", params, data => {
					that.buildList = data.list;
				})
			},
			getLocationsByBuildingId: function(buildingId) {
				var that = this;
				var params = {
					id: buildingId
				}
				baseUtile.httpSend("/getLocationsByBuildingId.html", params, data => {
					that.localtionList = data.list;
				})
			},
			quyerRoomInfo: function(locationId) {
				var that = this;
				var params = {
					locationId: locationId
				}
				baseUtile.httpSend("/quyerRoomInfo.html", params, data => {
					that.roomList = data.list;
				})
			},
			queryProprietor: function(roomId) {
				var that = this;
				var params = {
					roomId: roomId
				}
				baseUtile.httpSend("/queryProprietor.html", params, data => {
					that.proprietorList = data.list;
				})
			}
		},
		mounted() {
			this.queryHousingWy();
		}
	}
</script>


