<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-select placeholder="小区" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="楼栋" v-model="buildingId" :filterable=true>
						<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="queryLocation()">查询</el-button>
						<el-button type="primary" @click="addUnitVisible=true" v-if="sysFlag">添加</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" border style="width: 100%" v-loading="loading">
				<el-table-column prop="id" label="ID">
				</el-table-column>
				<el-table-column prop="housingName" label="小区名称">
				</el-table-column>
				<el-table-column prop="buildingName" label="楼栋名称">
				</el-table-column>
				<el-table-column prop="name" label="单元">
				</el-table-column>
				<el-table-column prop="remark" label="备注">
				</el-table-column>
				<el-table-column label="操作" v-if="sysFlag">
					<template scope="scope">
						<el-button type="text" size="small" @click="deleteLocation(scope.row.id)">删除</el-button>
						<el-button type="text" size="small" @click="deleteRoomInfoFromAlipay(scope.row.id)">删除房屋</el-button>
						<el-button type="text" size="small" @click="deleteBillAccountFromAlipay(scope.row.id)">删除账单</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="queryLocation" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="增加单元" v-model="addUnitVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="楼栋" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="buildingId" :filterable=true>
						<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="单元号" :rules="{required: true}">
					<el-input v-model="name"></el-input>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" :rows="4" v-model="remark"></el-input>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addUnitVisible = false">取消</el-button>
				<el-button type="primary" @click.native="saveLocation" :loading="addUnitLoding">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				list: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				addUnitVisible: false,
				houseList: [],
				buildList: [],
				addUnitLoding: false,
				housingId: "",
				buildingId: "",
				name: "",
				remark: "",
				sysFlag: false
			}
		},
		methods: {
			deleteLocation(id) {
				baseUtile.confirm("删除单元前请确定已经删除该单元下所属房屋?", res => {
					if(res) {
						var params = {
							"locationId": id
						}
						baseUtile.httpSend("/deleteLocation.html", params, data => {
							baseUtile.tip(data.msg);
							if(data.state == "1" || data.state == 1) {
								this.queryLocation();
							}
						})
					}
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			queryLocation: function(currPage) {
				if(!!currPage) {
					this.currPage = currPage;
				}
				var that = this;
				that.loading = true;
				var params = {
					currPage: this.currPage,
					housingId: this.housingId,
					buildingId: this.buildingId,
					locationId: "",
					type: "none"
				}
				baseUtile.httpSend("/queryLocation.html", params, data => {
					that.list = data.locations;
					that.loading = false;
					that.count = data.count;
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			getBuidingsById: function() {
				var that = this;
				var params = {
					id: this.housingId
				}
				baseUtile.httpSend("/getBuidingsById.html", params, data => {
					that.buildList = data.list;
				})
			},
			saveLocation: function() {
				var that = this;
				if(!this.housingId) {
					baseUtile.tip("请选择所属小区");
					return;
				}
				if(!this.buildingId) {
					baseUtile.tip("请选择所属楼栋");
					return;
				}
				if(!this.name) {
					baseUtile.tip("请输入单元号");
					return;
				}
				var params = {
					housingId: this.housingId,
					buildingId: this.buildingId,
					name: this.name,
					remark: this.remark,
				}
				that.addHouseLoding = true;
				baseUtile.httpSend("/saveLocation.html", params, data => {
					baseUtile.tip(data.msg, "success");
					that.addUnitVisible = false;
					that.addUnitLoding = false;
					this.queryLocation();
				})
			},
			deleteRoomInfoFromAlipay: function(id) {
				var params = {
					locationId: id
				}
				baseUtile.confirm("确定删除么?", res => {
					if(res) {
						baseUtile.httpSend("/deleteRoomInfoFromAlipay.html", params, data => {
							baseUtile.tip(data.msg, "success");
							that.queryLocation();
						})
					}
				})
			},
			deleteBillAccountFromAlipay: function(id) {
				var that = this;
				var params = {
					locationId: id
				}
				baseUtile.confirm("确定删除么?", res => {
					if(res) {
						baseUtile.httpSend("/deleteBillAccountFromAlipay.html", params, data => {
							baseUtile.tip(data.msg);
							that.quyerBuilding();
						})
					}
				})

			},
		},
		mounted() {
			var user = JSON.parse(sessionStorage.getItem('user'));
			this.queryLocation();
			this.queryHousingWy();
			if(!user.isSaler) {
				this.sysFlag = true;
			}
		}
	}
</script>


