<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-input placeholder="业主名称" v-model="name"></el-input>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="小区" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="楼栋" v-model="buildingId" :filterable=true @change="getLocationsByBuildingId">
						<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="单元" v-model="locationId" :filterable=true>
						<el-option v-for="item in localtionList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="queryProprietor(currPage)">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" border style="width: 100%" v-loading="loading">
				<el-table-column prop="housingName" label="小区名称">
				</el-table-column>
				<el-table-column prop="buildingName" label="楼栋名称">
				</el-table-column>
				<el-table-column prop="locationName" label="单元">
				</el-table-column>
				<el-table-column prop="room" label="房号">
				</el-table-column>
				<el-table-column prop="out_room_id" label="物业系统房屋号">
				</el-table-column>
				<el-table-column prop="roomInfoAddress" label="地址">
				</el-table-column>
				<el-table-column prop="name" label="业主姓名">
				</el-table-column>
				<el-table-column prop="card" label="身份证号">
				</el-table-column>
				<el-table-column prop="entryDate" label="入住时间">
				</el-table-column>
				<el-table-column label="操作" v-if="sysFlag">
					<template scope="scope">
						<el-button type="text" size="small" @click="openUpdateProprietor(scope.row)">修改</el-button>
						<el-button type="text" size="small" @click="deleteProprietor(scope.row.id)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="queryProprietor" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="业主修改" v-model="proprietorVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="业主姓名" :rules="{required: true}">
					<el-input v-model="proprietor.name"></el-input>
				</el-form-item>
				<el-form-item label="身份证号" :rules="{required: true}">
					<el-input v-model="proprietor.card"></el-input>
				</el-form-item>
				<el-form-item label="入住时间" :rules="{required: true}">
					<el-date-picker v-model="proprietor.entryDate" type="date" placeholder="选择日期" format="yyyy-MM-dd">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="电话" :rules="{required: true}">
					<el-input v-model="proprietor.phone"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="proprietorVisible = false">取消</el-button>
				<el-button type="primary" @click.native="updateProprietor" :loading="addHouseLoding">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				proprietor: {},
				list: [],
				buildList: [],
				localtionList: [],
				houseList: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				currPage: "",
				housingId: "",
				buildingId: "",
				locationId: "",
				housingId: "",
				buildingId: "",
				locationId: "",
				name: "",
				proprietorVisible: false,
				sysFlag: false
			}
		},
		methods: {
			deleteProprietor(id) {
				baseUtile.confirm("确定删除该业主?", res => {
					if(res) {
						var params = {
							"proprietorId": id
						}
						baseUtile.httpSend("/deleteProprietor.html", params, data => {
							baseUtile.tip(data.msg);
							if(data.state == "1" || data.state == 1) {
								this.queryProprietor(this.currPage);
							}
						})
					}
				})

			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			getBuidingsById: function(housingId) {
				var that = this;
				var params = {
					id: housingId
				}
				baseUtile.httpSend("/getBuidingsById.html", params, data => {
					that.buildList = data.list;
				})
			},
			getLocationsByBuildingId: function(buildingId) {
				var that = this;
				var params = {
					id: buildingId
				}
				baseUtile.httpSend("/getLocationsByBuildingId.html", params, data => {
					that.localtionList = data.list;
				})
			},
			queryProprietor: function(currPage) {
				if(!!currPage) {
					this.currPage = currPage;
				}
				var that = this;
				that.loading = true;
				var params = {
					name: this.name,
					currPage: this.currPage,
					housingId: this.housingId,
					buildingId: this.buildingId,
					locationId: this.locationId,
					type: "none"
				}
				baseUtile.httpSend("/queryProprietor.html", params, data => {
					that.list = data.list;
					that.loading = false;
					that.count = data.count;
				})
			},
			openUpdateProprietor(proprietor) {
				this.proprietor = JSON.parse(JSON.stringify(proprietor));
				this.proprietorVisible = true;
			},
			updateProprietor() {
				if(!this.proprietor.name) {
					baseUtile.tip("请输入业主名称!");
					return;
				}
				if(!this.proprietor.card) {
					baseUtile.tip("请输入业主身份证号码!");
					return;
				}
				if(!this.proprietor.phone) {
					baseUtile.tip("请输入业主手机号!");
					return;
				}
				this.addHouseLoding = true;
				var params = {
					id: this.proprietor.id,
					name: this.proprietor.name,
					card: this.proprietor.card,
					entryDate: baseUtile.dateFormat("yyyy-MM-dd", new Date(this.proprietor.entryDate)),
					phone: this.proprietor.phone
				}
				var that = this;
				baseUtile.httpSend("/saveUpdateProprietor.html", params, data => {
					if(data.status == "true" || data.status == true) {
						baseUtile.tip(data.msg, "success");
						that.proprietorVisible = false;
						that.queryProprietor(1);
						this.addHouseLoding = false;
					} else {
						baseUtile.tip(data.msg);
					}
				})
			}
		},
		mounted() {
			this.queryHousingWy();
			this.queryProprietor(1);
			var user = JSON.parse(sessionStorage.getItem('user'));
			if(!user.isSaler) {
				this.sysFlag = true;
			}
		}
	}
</script>


