<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-select placeholder="小区" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="quyerBuilding">查询</el-button>
						<el-button type="primary" @click="addHouseVisible=true" v-if="sysFlag">添加</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" border style="width: 100%" v-loading="loading">
				<el-table-column prop="id" label="ID">
				</el-table-column>
				<el-table-column prop="houseName" label="小区名称">
				</el-table-column>
				<el-table-column prop="name" label="楼栋名称">
				</el-table-column>
				<el-table-column prop="remark" label="备注">
				</el-table-column>
				<el-table-column label="操作">
					<template scope="scope">
						<el-button type="text" size="small" @click="deleteBuilding(scope.row.id)">删除</el-button>
						<el-button type="text" size="small" @click="deleteBillAccountFromAlipay(scope.row.id)">删除账单</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="quyerBuilding" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="增加楼栋" v-model="addHouseVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="housingId" :filterable=true>
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="楼栋名称" :rules="{required: true}">
					<el-input v-model="name"></el-input>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" :rows="4" v-model="remark"></el-input>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addHouseVisible = false">取消</el-button>
				<el-button type="primary" @click.native="saveBuilding" :loading="addHouseLoding">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				list: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				addHouseVisible: false,
				addHouseLoding: false,
				housingId: "",
				name: "",
				remark: "",
				houseList: [],
				sysFlag: false
			}
		},
		methods: {
			deleteBuilding(id) {
				baseUtile.confirm("删除楼栋前请确定已经删除该漏洞下所属单元及房屋?", res => {
					if(res) {
						var params = {
							"buildingId": id
						}
						baseUtile.httpSend("/deleteBuilding.html", params, data => {
							baseUtile.tip(data.msg);
							if(data.state == "1" || data.state == 1) {
								this.quyerBuilding();
							}
						})
					}
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			quyerBuilding: function(currPage) {
				if(!!currPage) {
					this.currPage = currPage;
				}
				var that = this;
				that.loading = true;
				var params = {
					currPage: that.currPage,
					name: that.name,
					housingId: that.housingId,
					type: "none"
				}
				baseUtile.httpSend("/quyerBuilding.html", params, data => {
					that.list = data.list;
					that.loading = false;
					that.count = data.count;
				})
			},
			deleteBillAccountFromAlipay: function(houseingId) {
				var that = this;
				var params = {
					houseingId: houseingId
				}
				baseUtile.confirm("确定删除么?", res => {
					if(res) {
						baseUtile.httpSend("/deleteBillAccountFromAlipay.html", params, data => {
							baseUtile.tip(data.msg);
							that.quyerBuilding();
						})
					}
				})

			},
			saveBuilding: function() {
				if(!this.housingId) {
					baseUtile.tip("请选择所属小区");
					return;
				}
				if(!this.name) {
					baseUtile.tip("请输入楼栋名称");
					return;
				}
				var that = this;
				var params = {
					housingId: this.housingId,
					name: this.name,
					remark: this.remark
				}
				that.addHouseLoding = true;
				baseUtile.httpSend("/saveBuilding.html", params, data => {
					baseUtile.tip(data.msg, "success");
					that.addHouseVisible = false;
					that.addHouseLoding = false;
					this.quyerBuilding();
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
		},
		mounted() {
			var user = JSON.parse(sessionStorage.getItem('user'));
			this.queryHousingWy();
			this.quyerBuilding();
			if(!user.isSaler) {
				this.sysFlag = true;
			}
		}
	}
</script>


