<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-input placeholder="物业名称" v-model="orgName"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input placeholder="公司名称" v-model="companyName"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="getDepartment_menu">查询</el-button>
						<el-button type="primary" @click="addVisible=true" v-if="sysFlag">添加</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-table :data="list" border style="width: 100%" v-loading="loading" element-loading-text="拼命加载中">
			<el-table-column prop="name" label="物业名称">
			</el-table-column>
			<el-table-column prop="parentName" label="上级物业">
			</el-table-column>
			<el-table-column prop="address" label="联系地址">
			</el-table-column>
			<el-table-column prop="phone" label="联系电话">
			</el-table-column>
			<el-table-column prop="contract_people" label="联系人" width="120">
			</el-table-column>
			<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button type="text" size="small" @click="openEditModel(scope.row)" v-if="sysFlag">修改</el-button>
					<el-button type="text" size="small" @click="department_del(scope.row.id)" v-if="sysFlag" v-loading.fullscreen.lock="fullscreenLoading">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" :current-page="currPage" @current-change="getDepartment_menu" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>
		<!--修改界面-->
		<el-dialog title="物业修改" v-model="formDialogVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="物业代码">
					<el-input disabled v-model="department.code"></el-input>
				</el-form-item>
				<el-form-item label="物业简码">
					<el-input disabled v-model="department.bycode"></el-input>
				</el-form-item>
				<el-form-item label="物业名称"  :rules="{required: true}">
					<el-input v-model="department.name"></el-input>
				</el-form-item>
				<el-form-item label="上级物业">
					<el-select placeholder="请选择" v-model="department.parentId">
						<el-option v-for="item in parentList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="联系人">
					<el-input v-model="department.contact_people"></el-input>
				</el-form-item>
				<el-form-item label="联系电话">
					<el-input v-model="department.phone"></el-input>
				</el-form-item>
				<el-form-item label="联系地址">
					<el-input v-model="department.address"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="formDialogVisible = false">取消</el-button>
				<el-button type="primary" @click.native="department_update" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="物业添加" v-model="addVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="物业名称" :rules="{required: true}">
					<el-input v-model="orgName2"></el-input>
				</el-form-item>
				<el-form-item label="上级物业">
					<el-select v-model="parentId" placeholder="请选择">
						<el-option v-for="item in parentList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="业务员">
					<el-select placeholder="请选择" v-model="owerName">

					</el-select>
				</el-form-item>
				<el-form-item label="联系人">
					<el-input v-model="orgUserName"></el-input>
				</el-form-item>
				<el-form-item label="联系电话">
					<el-input v-model="orgPhone" type="number"></el-input>
				</el-form-item>
				<el-form-item label="联系地址">
					<el-input v-model="orgAddress"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addVisible = false">取消</el-button>
				<el-button type="primary" @click="department_add" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>import {
	baseUtile
} from '../../api/baseUtile';
export default {
	data() {
		return {
			formDialogVisible: false,
			addVisible: false,
			orgName: '',
			companyName: '',
			list: [],
			count: 0,
			currPage: 1,
			pageSize: 10,
			loading: false,
			addLoading: false,
			orgName2: "",
			parentId: "",
			orgUserName: "",
			orgPhone: "",
			orgAddress: "",
			owerName: "",
			department: {},
			editLoading: false,
			sysFlag: false,
			parentList: [],
			fullscreenLoading: false
		}
	},
	methods: {
		getAllDepartment: function() {
			var that = this;
			var params = {
				type: "none"
			}
			that.loading = true;
			baseUtile.httpSend("/department_menu.do", params, function(data) {
				that.parentList = data.list;
			})
		},
		getDepartment_menu: function(currPage) {
			if(!!currPage) {
				this.currPage = currPage;
			}
			var that = this;
			var params = {
				currPage: that.currPage,
				orgName: that.orgName,
				companyName: that.companyName,
				type: "none"
			}
			that.loading = true;
			baseUtile.httpSend("/department_menu.do", params, function(data) {
				that.list = data.list;
				that.loading = false;
				that.count = data.count;
			})
		},
		department_add: function() {
			var that = this;
			if(!that.orgName2) {
				baseUtile.tip("请输入物业名称!")
				return;
			}
			if(that.orgName2.length < 4) {
				baseUtile.tip("物业名称不能少于四个字!")
				return;
			}
			var params = {
				orgName: that.orgName2,
				parentId: that.parentId,
				orgUserName: that.orgUserName,
				orgPhone: that.orgPhone,
				orgAddress: that.orgAddress
			}
			that.addLoading = true;
			baseUtile.httpSend("/department_add.do", params, function(data) {
				that.addLoading = false;
				baseUtile.tip("添加成功!", "success");
				that.addVisible = false;
				that.getDepartment_menu();
			})
		},
		department_del: function(id) {
			var that = this;
			baseUtile.confirm("确定删除么(删除会同步删除物业相关数据及账单，请耐心等待返回结果)?", res => {
				if(res) {
					that.fullscreenLoading = true;
					baseUtile.httpSend("/deleteDepartment.html", {
						id: id
					}, data => {
						that.fullscreenLoading = false;
						baseUtile.tip(data.msg);
						if(data.state == 1) {
							that.getDepartment_menu();
						}
						//							if(data.status == true || data.status == "true") {
						//								baseUtile.tip("删除成功!", "success");
						//								that.getDepartment_menu();
						//							} else {
						//								baseUtile.tip("删除失败!", "error");
						//							}

					})
				}
			})
		},
		openEditModel: function(department) {
			var that = this;
			that.formDialogVisible = true;
			that.department = JSON.parse(JSON.stringify(department));
		},
		department_update: function() {
			var that = this;
			if(!that.department.name) {
				baseUtile.tip("请输入组织机构名称");
				return;
			}
			if(that.department.name.length <= 4) {
				baseUtile.tip("组织机构名称不能少于四个字");
				return;
			}

			var params = {
				id: that.department.id,
				orgName: that.department.name,
				parentId: that.department.parentId,
				orgUserName: that.department.contact_people,
				orgPhone: that.department.phone,
				orgAddress: that.department.address
			}
			that.editLoading = true;
			baseUtile.httpSend("/department_update.do", params, data => {
				that.editLoading = false;
				baseUtile.tip("修改成功!", "success");
				that.formDialogVisible = false;
				that.getDepartment_menu();
			})
		}
	},
	mounted() {
		var user = JSON.parse(sessionStorage.getItem('user'));
		if(!user.isSaler) {
			this.sysFlag = true;
		}
		this.getDepartment_menu();
		this.getAllDepartment();
	}
}</script>


