<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" style="text-align: right;">
				<el-form-item>
					<el-button type="primary" @click="addModelVisible=true" v-if="sysFlag">添加</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" border style="width: 100%" v-loading="loading">
				<el-table-column prop="roleId" label="ID">
				</el-table-column>
				<el-table-column prop="roleName" label="角色名称">
				</el-table-column>
				<el-table-column prop="type" label="类型">
				</el-table-column>
				<el-table-column prop="statusString" label="状态">
				</el-table-column>
				<el-table-column label="操作">
					<template scope="scope">
						<el-button type="text" size="small" @click="openEditModel(scope.row)"  v-if="sysFlag">修改</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>

		<el-dialog title="修改角色" v-model="editModelVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="角色名称" :rule="{required: true}">
					<el-input v-model="role.roleName" ></el-input>
				</el-form-item>
			</el-form>
			<el-form label-width="100px">
				<el-form-item label="角色类型">
					<el-select v-model="role.type">
						<el-option :key="'manger'" :value="'manger'" label="超级管理员">
						</el-option>
						<el-option :key="'wyManger'" :value="'wyManger'" label="物业管理员">
						</el-option>
						<el-option :key="'saler'" :value="'saler'" label="业务员">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<el-form label-width="100px">
				<el-form-item label="状态">
					<el-radio-group v-model="role.status">
						<el-radio-button label="0">正常</el-radio-button>
						<el-radio-button label="1">停用</el-radio-button>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="editModelVisible = false">取消</el-button>
				<el-button type="primary" @click="updRole" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="添加角色" v-model="addModelVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="角色名称" :rule="{required: true}">
					<el-input v-model="addrole.roleName" ></el-input>
				</el-form-item>
			</el-form>
			<el-form label-width="100px">
				<el-form-item label="角色类型">
					<el-select v-model="addrole.type">
						<el-option :key="'manger'" :value="'manger'" label="超级管理员">
						</el-option>
						<el-option :key="'wyManger'" :value="'wyManger'" label="物业管理员">
						</el-option>
						<el-option :key="'saler'" :value="'saler'" label="业务员">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<el-form label-width="100px">
				<el-form-item label="状态">
					<el-radio-group v-model="addrole.status">
						<el-radio-button label="0">正常</el-radio-button>
						<el-radio-button label="1">停用</el-radio-button>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addModelVisible = false">取消</el-button>
				<el-button type="primary" @click="addRole" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				list: [],
				loading: false,
				editModelVisible: false,
				role: {},
				editLoading: false,
				addModelVisible: false,
				addrole: {
					roleName: "",
					type: "manger",
					status: 0
				},
				addLoading: false,
				sysFlag:false
			}
		},
		methods: {
			getRoleList: function() {
				var that = this;
				that.loading = true;
				baseUtile.httpSend("/roleList.html", null, data => {
					that.list = data.list;
					for(var i = 0; i < that.list.length; i++) {
						that.list[i].status == 0 ? that.list[i].statusString = '正常' : that.list[i].statusString = '停用';
					}
					that.loading = false;
				})
			},
			openEditModel: function(role) {
				this.role = JSON.parse(JSON.stringify(role));
				this.editModelVisible = true;
			},
			updRole: function() {
				var params = {
					roleId: this.role.roleId,
					roleName: this.role.roleName,
					type: this.role.type,
					status: this.role.status
				}
				var that = this;
				this.editLoading = true;
				baseUtile.httpSend("/updRole.html", params, data => {
					baseUtile.tip("修改成功!", "success");
					this.editLoading = false;
					this.editModelVisible = false;
					this.getRoleList();
				})
			},
			addRole: function() {
				if(!this.addrole.roleName) {
					baseUtile.tip("请输入角色名称");
					return;
				}
				var params = {
					roleName: this.addrole.roleName,
					assortment: this.addrole.type,
					status: this.addrole.status
				}
				this.addLoading = true;
				baseUtile.httpSend("/updRole.html", params, data => {
					baseUtile.tip("新增成功!", "success");
					this.addLoading = false;
					this.addModelVisible = false;
					this.getRoleList();
				})
			}
		},
		mounted() {
			var user = JSON.parse(sessionStorage.getItem('user'));
			if(user.username == "admin") {
				this.sysFlag = true;
			}
			this.getRoleList();
		}
	}
</script>


