<style>
	.el-input-group__prepend {
		width: 100px;
	}
</style>
<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" label-width="70px">
				<el-form-item label="用户">
					<el-input v-model="username"></el-input>
				</el-form-item>
				<!--<el-form-item label="开始日期">
					<el-date-picker v-model="startDate" type="date" placeholder="选择日期">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="截止日期">
					<el-date-picker v-model="endDate" type="date" placeholder="选择日期">
					</el-date-picker>
				</el-form-item>-->
			</el-form>
			<el-form :inline="true" label-width="70px">
				<!--<el-form-item label="门店/经销商">
					<el-input v-model="companyName"></el-input>
				</el-form-item>-->
				<el-form-item label="物业名称">
					<el-input v-model="departmentName"></el-input>
				</el-form-item>
				<el-form-item label="角色">
					<el-select v-model="roleId" placeholder="请选择" filterable>
						<el-option v-for="item in roleList" :key="item.roleId" :label="item.roleName" :value="item.roleId">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="queryUser(currpage)">查询</el-button>
						<el-button type="primary" @click="addDialogVisible=true" v-if="sysFlag">添加</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="list" style="width: 100%" v-loading="loading">
				<el-table-column type="expand">
					<template scope="props">
						<el-form label-position="left" inline class="demo-table-expand">
							<el-input v-model="props.row.email" :readonly=true>
								<template slot="prepend" width="100">邮箱</template>
							</el-input>
							<!--<el-input v-model="props.row.address" :readonly=true>
								<template slot="prepend" width="100">门店/经销商</template>
							</el-input>-->
							<el-input value="暂停" v-if="props.row.status==0" :readonly=true>
								<template slot="prepend" width="100">状态</template>
							</el-input>
							<el-input value="正常" v-if="props.row.status==1" :readonly=true>
								<template slot="prepend" width="100">状态</template>
							</el-input>
							<el-input v-model="props.row.regeditTime" :readonly=true>
								<template slot="prepend" width="100">注册时间</template>
							</el-input>
							<el-input v-model="props.row.roleName" :readonly=true>
								<template slot="prepend" width="100">角色</template>
							</el-input>
							<el-input v-model="props.row.departmentName" :readonly=true>
								<template slot="prepend" width="100">物业名称</template>
							</el-input>
						</el-form>
					</template>
				</el-table-column>
				<el-table-column prop="userId" label="ID" width="80">
				</el-table-column>
				<el-table-column prop="username" label="用户名" :show-overflow-tooltip="true" width="150">
				</el-table-column>
				<el-table-column prop="ptpwd" label="口令" :show-overflow-tooltip="true" width="100">
				</el-table-column>
				<el-table-column prop="certification==0?'未上传':certification==1?'待审核':'已认证'" label="认证状态" :show-overflow-tooltip="true" width="100">
				</el-table-column>
				<el-table-column prop="qq" label="QQ" :show-overflow-tooltip="true" width="150">
				</el-table-column>
				<el-table-column prop="phone" label="电话" :show-overflow-tooltip="true" width="150">
				</el-table-column>
				<el-table-column label="操作">
					<template scope="scope">
						<el-button type="text" size="small" @click="openEditUserModel(scope.row)" v-if="sysFlag">修改</el-button>
						<el-button type="text" size="small" @click="changeUserState(scope.row.userId,0)" v-if="scope.row.status==1&&sysFlag">暂停</el-button>
						<el-button type="text" size="small" @click="changeUserState(scope.row.userId,1)" v-if="scope.row.status==0&&sysFlag">激活</el-button>
						<el-button type="text" size="small" @click="resetUserPasword(scope.row.userId)">密码重置</el-button>
						<el-button type="text" size="small" @click="deleteUser(scope.row.userId)" v-if="sysFlag">删除账户</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="queryUser" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="添加用户" v-model="addDialogVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="用户名" :rules="{required: true}">
					<el-input v-model="addUser.username"></el-input>
				</el-form-item>
				<el-form-item label="昵称" :rules="{required: true}">
					<el-input v-model="addUser.nick"></el-input>
				</el-form-item>
				<el-form-item label="邮箱">
					<el-input v-model="addUser.email"></el-input>
				</el-form-item>
				<el-form-item label="QQ">
					<el-input v-model="addUser.qq"></el-input>
				</el-form-item>
				<el-form-item label="电话">
					<el-input v-model="addUser.phone"></el-input>
				</el-form-item>
				<el-form-item label="角色" :rules="{required: true}">
					<el-select v-model="addUser.roleId" placeholder="请选择" filterable>
						<el-option v-for="item in roleList" :key="item.roleId" :label="item.roleName" :value="item.roleId">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="物业" :rules="{required: true}">
					<el-select v-model="addUser.department" filterable placeholder="请选择">
						<el-option v-for="item in departmentList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="状态">
					<el-radio-group v-model="addUser.status">
						<el-radio-button label="1">正常</el-radio-button>
						<el-radio-button label="0">暂停</el-radio-button>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addDialogVisible = false">取消</el-button>
				<el-button type="primary" @click="addUserInfo" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="修改用户" v-model="editDialogVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="用户名" readonly :rules="{required: true}">
					<el-input v-model="editUser.username"></el-input>
				</el-form-item>
				<el-form-item label="昵称" :rules="{required: true}">
					<el-input v-model="editUser.nick"></el-input>
				</el-form-item>
				<el-form-item label="邮箱">
					<el-input v-model="editUser.email"></el-input>
				</el-form-item>
				<el-form-item label="QQ">
					<el-input v-model="editUser.qq"></el-input>
				</el-form-item>
				<el-form-item label="电话">
					<el-input v-model="editUser.phone"></el-input>
				</el-form-item>
				<el-form-item label="角色" :rules="{required: true}">
					<el-select v-model="editUser.roleId" placeholder="请选择" filterable>
						<el-option v-for="item in roleList" :key="item.roleId" :label="item.roleName" :value="item.roleId">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="物业" :rules="{required: true}">
					<el-select v-model="editUser.departmentId" filterable placeholder="请选择">
						<el-option v-for="item in departmentList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<!--<el-form-item label="省">
					<el-input v-model="editUser.province"></el-input>
				</el-form-item>
				<el-form-item label="市">
					<el-input v-model="editUser.city"></el-input>
				</el-form-item>
				<el-form-item label="所在地">
					<el-input v-model="editUser.address"></el-input>
				</el-form-item>
				<el-form-item label="银行名称">
					<el-input v-model="editUser.bankName"></el-input>
				</el-form-item>
				<el-form-item label="开户行地址">
					<el-input v-model="editUser.bankAddress"></el-input>
				</el-form-item>
				<el-form-item label="银行卡姓名">
					<el-input v-model="editUser.bankUserName"></el-input>
				</el-form-item>
				<el-form-item label="银行卡卡号">
					<el-input v-model="editUser.bankNo"></el-input>
				</el-form-item>
				<el-form-item label="身份证号码">
					<el-input v-model="editUser.certificate"></el-input>
				</el-form-item>-->
				<el-form-item label="状态">
					<el-radio-group v-model="editUser.status">
						<el-radio-button label="1">正常</el-radio-button>
						<el-radio-button label="0">暂停</el-radio-button>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="editDialogVisible = false">取消</el-button>
				<el-button type="primary" @click="editUserInfo" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				list: [],
				roleList: [],
				departmentList: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				departmentName: "",
				companyName: "",
				username: "",
				roleId: "",
				startDate: "",
				endDate: "",
				department: "",
				editUser: {},
				addDialogVisible: false,
				addUser: {
					username: "",
					nick: "",
					email: "",
					qq: "",
					phone: "",
					roleId: "",
					member_levelId: "",
					department: "",
					status: 1
				},
				addLoading: false,
				editDialogVisible: false,
				editLoading: false,
				dialogImageUrl: '',
				dialogVisible: false,
				sysFlag: false
			}
		},
		methods: {
			queryUser: function(currPage) {
				if(!!currPage) {
					this.currPage = currPage;
				}
				var that = this;
				var startDate = "";
				var endDate = "";
				if(!!that.startDate) {
					baseUtile.dateFormat("yyyy-MM-dd", that.startDate)
				}
				if(!!that.endDate) {
					baseUtile.dateFormat("yyyy-MM-dd", that.endDate)
				}
				var params = {
					currPage: that.currPage,
					departmentName: that.departmentName,
					companyName: that.companyName,
					username: that.username,
					roleId: that.roleId,
					startDate: startDate,
					endDate: endDate
				}
				this.loading = true;
				baseUtile.httpSend("/queryUser.html", params, data => {
					that.list = new Array();
					data.result.forEach(tmp => {
						if(tmp.username != "youliang") {
							that.list.push(tmp);
						}
					})
					that.loading = false;
					that.count = data.totalRecord;
				})
			},
			getRoleList: function() {
				var that = this;
				baseUtile.httpSend("/roleList.html", null, data => {
					that.roleList = data.list;
				})
			},
			getDepartment_menu: function() {
				var that = this;
				var params = {
					orgName: "",
					companyName: "",
					type: "none"
				}
				that.loading = true;
				baseUtile.httpSend("/department_menu.do", params, function(data) {
					that.departmentList = data.list;
				})
			},
			addUserInfo: function() {
				var that = this;
				if(!this.addUser.username) {
					baseUtile.tip("请输入用户名!");
					return;
				}
				if(!this.addUser.nick) {
					baseUtile.tip("请输入昵称!");
					return;
				}
				if(!this.addUser.roleId) {
					baseUtile.tip("请选择角色!");
					return;
				}
				if(!this.addUser.department) {
					baseUtile.tip("请选择物业!");
					return;
				}
				var params = {
					username: this.addUser.username,
					nick: this.addUser.nick,
					email: this.addUser.email,
					qq: this.addUser.qq,
					phone: this.addUser.phone,
					roleId: this.addUser.roleId,
					member_levelId: this.addUser.member_levelId,
					department: this.addUser.department,
					status: this.addUser.status
				}
				that.addLoading = true;
				baseUtile.httpSend("/addUser.html", params, data => {
					baseUtile.tip("添加成功!", "success");
					that.addLoading = false;
					that.addDialogVisible = false;
					that.queryUser(that.currPage);
				})
			},
			changeUserState: function(userId, state) {
				var params = {
					userId: userId,
					status: state
				}
				var that = this;
				baseUtile.httpSend("/updStatus.html", params, data => {
					baseUtile.tip("操作成功!", "success");
					that.queryUser(that.currPage);
				})
			},
			resetUserPasword: function(userId) {
				var params = {
					userId: userId
				}
				var that = this;
				baseUtile.httpSend("/resetUserPasword.html", params, data => {
					baseUtile.tip("重置密码成功!", "success");
					that.queryUser(that.currPage);
				})
			},
			deleteUser: function(userId) {
				var params = {
					userId: userId
				}
				var that = this;
				baseUtile.confirm("确定删除该用户么?", res => {
					if(res) {
						baseUtile.httpSend("/deleteUser.html", params, data => {
							baseUtile.tip("删除成功!", "success");
							that.queryUser(that.currPage);
						})
					}
				})
			},
			openEditUserModel: function(user) {
				this.editDialogVisible = true;
				this.editUser = JSON.parse(JSON.stringify(user));
			},
			editUserInfo: function() {
				if(!this.editUser.nick) {
					baseUtile.tip("请输入昵称!");
					return;
				}
				var departmentCode;
				this.departmentList.forEach(item => {
					if(item.id == this.editUser.departmentId)
						departmentCode = item.code;
				})
				var params = {
					userId: this.editUser.userId,
					nick: this.editUser.nick,
					email: this.editUser.email,
					roleId: this.editUser.roleId,
					qq: this.editUser.qq,
					phone: this.editUser.phone,
					province: this.editUser.province,
					city: this.editUser.city,
					address: this.editUser.address,
					bankName: this.editUser.bankName,
					bankAddress: this.editUser.bankAddress,
					bankUserName: this.editUser.bankUserName,
					bankNo: this.editUser.bankNo,
					certificate: this.editUser.certificate,
					member_levelId: this.editUser.member_levelId,
					status: this.editUser.status,
					certification: this.editUser.certification,
					departmentId: this.editUser.departmentId,
					departmentCode: departmentCode
				}
				var that = this;
				this.editLoading = true;
				baseUtile.httpSend("/updUser.html", params, data => {
					if(data.r == false || data.r == "false") {
						baseUtile.tip(data.m);
					} else {
						baseUtile.tip("修改成功!", "success");
						that.editDialogVisible = false;
					}
					that.editLoading = false;
					this.queryUser(1);
				})
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			}

		},
		mounted() {
			this.getRoleList();
			this.queryUser(1);
			this.getDepartment_menu();
			var user = JSON.parse(sessionStorage.getItem('user'));
			if(user.username == "admin") {
				this.sysFlag = true;
			}
		}
	}
</script>


