<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item label="小区">
					<el-select placeholder="请选择" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="楼栋">
					<el-select placeholder="请选择" v-model="buildingId" :filterable=true @change="getLocationsByBuildingId">
						<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="单元">
					<el-select placeholder="请选择" v-model="locationId" :filterable=true>
						<el-option v-for="item in localtionList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<!--<el-form-item label="入住">
					<el-select placeholder="请选择" v-model="entryStatus" :filterable=true>
						<el-option :label="全部" :value="">
						</el-option>
						<el-option :label="已入住" :value="'1'">
						</el-option>
						<el-option :label="未入住" :value="'0'">
						</el-option>
					</el-select>
				</el-form-item>-->
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="quyerRoomInfo">查询</el-button>
						<el-button type="primary" @click="addHouseVisible=true" v-if="sysFlag">添加</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
			<el-form :inline="true" v-if="sysFlag">
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="importModelVisible=true,importLoading=false">导入房屋</el-button>
						<el-button type="primary" @click="roomInfoBachUpload">同步至支付宝(每次200条)</el-button>
						<el-button type="primary" @click="downloadRoomModule">下载房屋模板</el-button>
						<el-button type="danger" :loading="btnloading" @click="deleteSelectedRoom">删除选中房屋</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-tabs v-model="activeName" @tab-click="tabHandleClick">
				<el-tab-pane label="全部" name="-1"></el-tab-pane>
				<el-tab-pane label="已入住" name="1"></el-tab-pane>
				<el-tab-pane label="未入住" name="0"></el-tab-pane>
				<el-tab-pane label="已同步" name="2"></el-tab-pane>
				<el-tab-pane label="未同步" name="3"></el-tab-pane>
			</el-tabs>
			<el-table @selection-change="handleSelectionChange" :data="list" border style="width: 100%" v-loading="loading">
				<el-table-column type="selection" width="50">
				</el-table-column>
				<el-table-column prop="housingName" label="小区名称">
				</el-table-column>
				<el-table-column prop="buildingName" label="楼栋名称">
				</el-table-column>
				<el-table-column prop="locationName" label="单元">
				</el-table-column>
				<el-table-column prop="room" label="房号">
				</el-table-column>
				<el-table-column prop="out_room_id" label="物业系统编号">
				</el-table-column>
				<el-table-column prop="room_id" label="支付宝房间号">
				</el-table-column>
				<el-table-column prop="address" label="地址">
				</el-table-column>
				<el-table-column prop="stateString" label="状态">
				</el-table-column>
				<el-table-column prop="remark" label="备注">
				</el-table-column>
				<el-table-column label="操作" v-if="sysFlag">
					<template scope="scope">
						<el-button type="text" size="small" @click="roominfoUpload(scope.row.id)" v-show="scope.row.sendStatus==0">上传支付宝</el-button>
						<el-button type="text" size="small" @click="openProprietorEntry(scope.row)" v-show="scope.row.entryStatus==0">业主入住</el-button>
						<el-button type="text" size="small" @click="delroom(scope.row.id)">删除房屋</el-button>
						<!--<el-button type="text" size="small" @click="deleteRoom(scope.row.id)">删除</el-button>-->
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="quyerRoomInfo" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="增加房屋" v-model="addHouseVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="house.housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="楼栋" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="house.buildingId" :filterable=true @change="getLocationsByBuildingId">
						<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="单元" :rules="{required: true}">
					<el-select placeholder="请选择" v-model="house.locationId" :filterable=true>
						<el-option v-for="item in localtionList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="房屋号" :rules="{required: true}">
					<el-input v-model="house.room"></el-input>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" :rows="4" v-model="house.remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addHouseVisible = false">取消</el-button>
				<el-button type="primary" @click.native="saveRoomInfo" :loading="addHouseLoding">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="业主入住" v-model="proprietorEntryVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="小区">
					<el-input :readonly="true" v-model="proprietorEntry.housingName"></el-input>
				</el-form-item>
				<el-form-item label="楼栋">
					<el-input :readonly="true" v-model="proprietorEntry.buildName"></el-input>
				</el-form-item>
				<el-form-item label="房屋地址">
					<el-input :readonly="true" v-model="proprietorEntry.productName"></el-input>
				</el-form-item>
				<el-form-item label="业主姓名">
					<el-input v-model="proprietorEntry.name"></el-input>
				</el-form-item>
				<el-form-item label="身份证号">
					<el-input v-model="proprietorEntry.card"></el-input>
				</el-form-item>
				<el-form-item label="入住时间" :rules="{required: true}">
					<el-date-picker v-model="proprietorEntry.entryDate" type="date" placeholder="选择日期" format="yyyy-MM-dd">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="电话" :rules="{required: true}">
					<el-input v-model="proprietorEntry.phone"></el-input>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" :rows="4" v-model="proprietorEntry.remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="proprietorEntryVisible = false">取消</el-button>
				<el-button type="primary" @click.native="saveProprietorEntry" :loading="addHouseLoding">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="导入房屋信息" v-model="importModelVisible" :close-on-click-modal="false">
			<el-form label-width="200px">
				<el-form-item label="请选择需要导入的房屋信息">
					<el-upload class="upload-demo" name="excelRoomInfoFile" ref="upload" action="/readRoomInfoExcel.html" :on-success="uploadSuccess" :auto-upload="false">
						<el-button slot="trigger" size="small" type="primary">选取文件</el-button>
						<div slot="tip" class="el-upload__tip">只能上传excel文件</div>
					</el-upload>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="importModelVisible = false">取消</el-button>
				<el-button type="primary" @click="submitUpload" :loading="importLoading">开始导入</el-button>
			</div>
		</el-dialog>

		<el-dialog title="导入结果" v-model="resultVisible" :close-on-click-modal="false">
			<el-row style="text-align: center;">{{successResult}}</el-row>
			<el-row v-for="item in resultList" style="text-align: center;">{{item}}</el-row>
			<div slot="footer" class="dialog-footer">
				<el-button @click="resultVisible = false">确定</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				list: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				currPage: "",
				housingId: "",
				buildingId: "",
				locationId: "",
				entryStatus: "",
				houseList: [],
				buildList: [],
				localtionList: [],
				addHouseLoding: false,
				addHouseVisible: false,
				house: {
					housingId: "",
					buildingId: "",
					locationId: "",
					room: "",
					remark: "",
				},
				proprietorEntryVisible: false,
				housingId: "",
				buildingId: "",
				locationId: "",
				entryStatus: "",
				importModelVisible: false,
				importLoading: false,
				activeName: '-1',
				proprietorEntry: {
					id: "",
					housingName: "",
					buildName: "",
					productName: "",
					name: "",
					card: "",
					entryDate: new Date(),
					phone: "",
					remark: "",
				},
				sendStatus: "",
				btnloading: false,
				multipleSelection: [],
				resultVisible: false,
				successResult: "",
				resultList: [],
				sysFlag: false
			}
		},
		methods: {
			deleteRoom(id) {
				baseUtile.confirm("确定删除该房屋?", res => {
					if(res) {
						var params = {
							"roomId": id
						}
						baseUtile.httpSend("/deleteRoom.html", params, data => {
							baseUtile.tip(data.msg);
							if(data.state == "1" || data.state == 1) {
								this.quyerRoomInfo(this.currPage);
							}
						})
					}
				})
			},
			downloadRoomModule() {
				window.location.href = "/downloadfile/room.xls";
			},
			deleteSelectedRoom: function() {
				if(this.multipleSelection.length <= 0) {
					baseUtile.tip("请选择需要删除的房屋");
					return;
				}
				var count = 0;
				this.btnloading = true;
				for(var i = 0; i < this.multipleSelection.length; i++) {
					var params = {
						roomId: this.multipleSelection[i].id
					}
					baseUtile.httpSend("/deleteRoomInfoFromAlipayById.html", params, data => {
						count++;
						if(count == this.multipleSelection.length) {
							baseUtile.tip("删除成功!", "success");
							this.quyerRoomInfo(1);
							this.btnloading = false;
						}
					})
				}
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			delroom: function(roomId) {
				var that = this;
				baseUtile.confirm("确定删除该房屋信息?", res => {
					var params = {
						roomId: roomId
					}
					baseUtile.httpSend("/deleteRoomInfoFromAlipayById.html", params, data => {
						baseUtile.tip(data.msg, "success");
						that.quyerRoomInfo(1);
					})
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			quyerRoomInfo: function(currPage) {
				if(!!currPage) {
					this.currPage = currPage;
				}
				var that = this;
				that.loading = true;
				var params = {
					currPage: this.currPage,
					housingId: this.housingId,
					buildingId: this.buildingId,
					locationId: this.locationId,
					entryStatus: this.entryStatus,
					sendStatus: this.sendStatus,
					type: "none"
				}
				baseUtile.httpSend("/quyerRoomInfo.html", params, data => {
					that.list = data.list;
					that.list.forEach(function(item) {
						switch(item.sendStatus) {
							case 0:
								item.stateString = '未同步';
								break;
							case 1:
								item.stateString = '已同步';
								break;
						}
					})
					that.loading = false;
					that.count = data.count;
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			getBuidingsById: function(housingId) {
				var that = this;
				var params = {
					id: housingId
				}
				baseUtile.httpSend("/getBuidingsById.html", params, data => {
					that.buildList = data.list;
				})
			},
			getLocationsByBuildingId: function(buildingId) {
				var that = this;
				var params = {
					id: buildingId
				}
				baseUtile.httpSend("/getLocationsByBuildingId.html", params, data => {
					that.localtionList = data.list;
				})
			},
			saveRoomInfo: function() {
				var that = this;
				if(!this.house.housingId) {
					baseUtile.tip("请选择所属小区");
					return;
				}
				if(!this.house.buildingId) {
					baseUtile.tip("请选择所属楼栋");
					return;
				}
				if(!this.house.locationId) {
					baseUtile.tip("请选择所属单元号");
					return;
				}
				if(!this.house.room) {
					baseUtile.tip("请输入房屋号");
					return;
				}
				var params = {
					housingId: this.house.housingId,
					buildingId: this.house.buildingId,
					locationId: this.house.locationId,
					room: this.house.room,
					remark: this.house.remark,
				}
				that.addHouseLoding = true;
				baseUtile.httpSend("/saveRoomInfo.html", params, data => {
					that.addHouseLoding = false;
					baseUtile.tip(data.msg, "success");
					that.addHouseVisible = false;
					this.quyerRoomInfo(1);
				})
			},
			roominfoUpload: function(id) {
				var params = {
					id: id
				}
				baseUtile.httpSend("/roominfoUpload.html", params, data => {
					baseUtile.tip(data.msg, "success");
					this.quyerRoomInfo(1);
				})
			},
			roomInfoBachUpload: function() {
				var params = {
					id: ""
				}
				baseUtile.httpSend("/roomInfoBachUpload.html", params, data => {
					baseUtile.tip(data.msg, "success");
					this.quyerRoomInfo(1);
				})
			},
			deleteRoomInfoFromAlipay: function() {
				baseUtile.confirm("确定删除所有房屋信息么?", res => {
					if(res) {
						var params = {
							id: ""
						}
						this.btnloading = true;
						baseUtile.httpSend("/deleteRoomInfoFromAlipay.html", params, data => {
							baseUtile.tip(data.msg, "success");
							this.quyerRoomInfo(1);
							this.btnloading = false;
						})

					}
				})
			},
			submitUpload() {
				this.importLoading = true;
				this.$refs.upload.submit();
			},
			uploadSuccess(res) {
				this.resultList = res.result;
				this.successResult = res.success;
				this.resultVisible = true;
				this.importModelVisible = false;
				this.importLoading = false;
			},
			tabHandleClick(tab) {
				this.entryStatus = "";
				this.sendStatus = "";
				if(tab.name == -1) {
					this.entryStatus = "";
				}

				if(tab.name == 0 || tab.name == "0") {
					this.entryStatus = 0;
				}
				if(tab.name == 1 || tab.name == "1") {
					this.entryStatus = 1;

				}
				if(tab.name == 2 || tab.name == "2") {
					this.sendStatus = 1;

				}
				if(tab.name == 3 || tab.name == "3") {
					this.sendStatus = 0;

				}
				this.quyerRoomInfo(1);
			},
			openProprietorEntry(room) {
				this.proprietorEntry.id = room.id;
				this.proprietorEntry.housingName = room.housingName;
				this.proprietorEntry.buildName = room.buildingName;
				this.proprietorEntry.productName = room.address;
				this.proprietorEntryVisible = true;
			},
			saveProprietorEntry() {
				if(!this.proprietorEntry.name) {
					baseUtile.tip("请输入业主名称!");
					return;
				}
				//			if(!this.proprietorEntry.card) {
				//				baseUtile.tip("请输入业主身份证号码!");
				//				return;
				//			}
				//			if(!this.proprietorEntry.phone) {
				//				baseUtile.tip("请输入业主手机号!");
				//				return;
				//			}
				this.addHouseLoding = true;
				var params = {
					id: this.proprietorEntry.id,
					name: this.proprietorEntry.name,
					card: this.proprietorEntry.card,
					entryDate: baseUtile.dateFormat("yyyy-MM-dd", this.proprietorEntry.entryDate),
					phone: this.proprietorEntry.phone,
					remark: this.proprietorEntry.remark,
				}
				var that = this;
				baseUtile.httpSend("/saveProprietorEntry.do", params, data => {
					that.addHouseLoding = false;
					if(data.status == "true" || data.status == true) {
						baseUtile.tip(data.msg, "success");
						that.proprietorEntryVisible = false;
						that.quyerRoomInfo(1);
					} else {
						baseUtile.tip(data.msg);
					}
				})
			}
		},
		mounted() {
			var user = JSON.parse(sessionStorage.getItem('user'));
			this.quyerRoomInfo(1);
			this.queryHousingWy();
			if(!user.isSaler) {
				this.sysFlag = true;
			}
		}
	}
</script>


