<style>
	table {
		text-align: center;
	}
	
	.money {
		color: #58B7FF;
		font-size: 26px;
		line-height: 90px;
		width: 100%;
		text-align: center;
	}
</style>
<template>
	<!--<section class="chart-container">
		<el-row>
			<el-col :span="24">
				<div id="chartColumn" style="width:100%; height:400px;"></div>
			</el-col>
			<el-col :span="24">
				<div id="chartLine" style="width:100%; height:400px;"></div>
			</el-col>
		</el-row>
	</section>-->
	<section>

		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;padding-top: 10px;">
			<el-form :inline="true">
				<el-form-item label="业务员" v-if="user.username=='admin'">
					<el-select placeholder="请选择" v-model="saleUserId" :filterable=true>
						<el-option v-for="item in saleUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="小区">
					<el-select placeholder="请选择" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="开始账期">
					<el-date-picker type="date" v-model="startDate" placeholder="选择月份">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="结束账期">
					<el-date-picker type="date" v-model="endDate" placeholder="选择月份">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="开始付款日期">
					<el-date-picker type="date" v-model="startPaymentDate" placeholder="选择月份">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="结束付款日期">
					<el-date-picker type="date" v-model="endPaymentDate" placeholder="选择月份">
					</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getIndexData">查询</el-button>
					<el-button type="primary" @click="showAddSmsVisible = true" v-if="showAddSms">短信充值</el-button>

					<!--<el-button-group>
						<el-button type="primary" @click="billAccountBachUpload">批量同步账单</el-button>
						<el-button type="primary" @click="importModelVisible=true">导入待付款账单</el-button>
					</el-button-group>-->
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-table :data="data" border style="width: 100%" v-loading="loading">
				<el-table-column label="收款金额">
					<template scope="scope">
						<span class="money">{{(scope.row.weixin+scope.row.alipay+scope.row.xianjin).toFixed(2)}}</span>
						<table style="width: 100%;">
							<tr>
								<td>支付宝</td>
								<td>微信</td>
								<td>现金</td>
							</tr>
							<tr>
								<td>{{scope.row.alipay.toFixed(2)}}</td>
								<td>{{scope.row.weixin.toFixed(2)}}</td>
								<td>{{scope.row.xianjin.toFixed(2)}}</td>
							</tr>
						</table>
					</template>
				</el-table-column>
				<el-table-column label="未收款金额">
					<template scope="scope">
						<span class="money">{{scope.row.noPayMoney}}</span>
					</template>
				</el-table-column>
				<el-table-column label="已收账单数">
					<template scope="scope">
						<span class="money">{{scope.row.payConut}}</span>
					</template>
				</el-table-column>
				<el-table-column label="未收账单数">
					<template scope="scope">
						<span class="money">{{scope.row.noPayCount}}</span>
					</template>
				</el-table-column>
				<el-table-column label="未同步数">
					<template scope="scope">
						<span class="money">{{scope.row.noSend}}</span>
					</template>
				</el-table-column>
				<el-table-column label="已同步数">
					<template scope="scope">
						<span class="money">{{scope.row.send}}</span>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<el-col :span="24">
			<el-col :span="12">
				<div id="cricleChart" style="width: 100%;height: 300px;"></div>
			</el-col>
			<el-col :span="12">
				<div id="chartColumn" style="width: 100%;height: 300px"></div>
			</el-col>
		</el-col>

		<el-dialog title="导入账单" v-model="importModelVisible" :close-on-click-modal="false">
			<el-form label-width="200px">
				<el-form-item label="请选择需要导入的账单">
					<el-upload class="upload-demo" name="excelFile" ref="upload" action="/readExcel.html" :on-success="uploadSuccess" :auto-upload="false">
						<el-button slot="trigger" size="small" type="primary">选取文件</el-button>
						<div slot="tip" class="el-upload__tip">只能上传excel文件</div>
					</el-upload>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="importModelVisible = false">取消</el-button>
				<el-button type="primary" @click="submitUpload" :loading="importLoading">开始导入</el-button>
			</div>
		</el-dialog>

		<el-dialog title="短信充值" v-model="showAddSmsVisible" :close-on-click-modal="false">
			<el-form label-width="200px">
				<el-form-item label="小区">
					<el-select placeholder="请选择" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="条数">
					<el-input-number v-model="smsCount" :min="1" :max="1000"></el-input-number>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="showAddSmsVisible = false">取消</el-button>
				<el-button type="primary" @click="addSmsCount" :loading="importLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import echarts from 'echarts'
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				chartColumn: null,
				chartBar: null,
				chartLine: null,
				chartPie: null,
				loading: false,
				housingId: "",
				startDate: new Date(new Date().setFullYear(2000, 0, 1)),
				endDate: new Date(new Date().setFullYear(new Date().getFullYear(), 11, 31)),
				startPaymentDate: new Date(new Date().setFullYear(2000, 0, 1)),
				endPaymentDate: new Date(new Date().setFullYear(new Date().getFullYear(), 11, 31)),
				data: [{
					alipay: 0.00,
					weixin: 0.00,
					xianjin: 0.00,
					noPayMoney: 0.00,
					payConut: 0,
					noPayCount: 0
				}],
				importModelVisible: false,
				importLoading: false,
				saleUserList: [],
				saleUserId: "",
				smsCount: 1,
				showAddSms: false,
				showAddSmsVisible: false,
				user: {}
			}
		},
		methods: {
			addSmsCount() {
				if(!this.housingId) {
					baseUtile.tip("请选择小区!");
					return;
				}
				var params = {
					housingId: this.housingId,
					smsCount: this.smsCount
				}
				this.importLoading = true;
				baseUtile.httpSend("/addSmsCount.html", params, data => {
					baseUtile.tip(data.msg, "success");
					this.importLoading = false;
					this.showAddSmsVisible = false;
				})
			},
			getAllSaleUser() {
				baseUtile.httpSend("/getAllSaleUser.html", null, data => {
					this.saleUserList = data.saleUser;
				})
			},
			drawColumnChart() {
				var that = this;
				this.chartColumn = echarts.init(document.getElementById('chartColumn'));
				this.chartColumn.setOption({
					title: {
						text: '账单笔数',
						x: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c} ({d}%)"
					},
					legend: {
						orient: 'vertical',
						left: 'left',
						data: ['已缴', '待缴']
					},
					series: [{
						name: '账单数量',
						type: 'pie',
						radius: '55%',
						center: ['50%', '60%'],
						data: [{
								value: that.data[0].payConut,
								name: '已缴'
							},
							{
								value: that.data[0].noPayCount,
								name: '待缴'
							},
						],
						color: ['#5da9ff', '#C0CCDA'],

						itemStyle: {
							emphasis: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				});
			},
			drawCricleChart() {
				var that = this;
				var option = {
					title: {
						text: '账单金额',
						x: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c} ({d}%)"
					},
					legend: {
						orient: 'vertical',
						left: 'left',
						data: ['已缴', '未缴']
					},
					color: ['#5da9ff', '#C0CCDA'],
					series: [{
						name: '金额',
						type: 'pie',
						radius: '55%',
						center: ['50%', '60%'],
						data: [{
								value: that.data[0].weixin + that.data[0].alipay + that.data[0].xianjin,
								name: '已缴'
							},
							{
								value: that.data[0].noPayMoney,
								name: '未缴'
							}
						],
						itemStyle: {
							emphasis: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				};
				this.chartColumn = echarts.init(document.getElementById('cricleChart'));
				this.chartColumn.setOption(option);
			},
			drawCharts() {
				this.drawCricleChart();
				this.drawColumnChart();
			},
			getIndexData() {
				var params = {
					startDate: baseUtile.dateFormat("yyyyMMdd", this.startDate),
					endDate: baseUtile.dateFormat("yyyyMMdd", this.endDate),
					startPaymentDate: baseUtile.dateFormat("yyyy-MM-dd", this.startPaymentDate),
					endPaymentDate: baseUtile.dateFormat("yyyy-MM-dd", this.endPaymentDate),
					housingId: this.housingId,
					saleUserId: this.saleUserId
				}
				this.loading = true;
				var that = this;
				baseUtile.httpSend("/getCountAccountData.html", params, data => {
					that.data = [];
					that.data.push(data.data);
					that.loading = false;
				})
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
					that.getIndexData();
				})
			},
			billAccountBachUpload: function() {
				var that = this;
				var params = {
					id: ""
				}
				baseUtile.httpSend("/billAccountBachUpload.html", params, data => {
					baseUtile.alert(data.msg);
				})
			},
			submitUpload() {
				this.importLoading = true;
				this.$refs.upload.submit();
			},
			uploadSuccess(res) {
				var that = this;
				baseUtile.alert(res);
				that.importModelVisible = false;
				that.importLoading = false;
			},
		},
		mounted: function() {
			var user = JSON.parse(sessionStorage.getItem('user'));
			this.user = user;
			if(user.username == "youliang") {
				this.showAddSms = true;
			}
			this.drawCharts();
			this.getAllSaleUser();
			this.queryHousingWy();
		},
		updated: function() {
			this.drawCharts();
		}
	}
</script>

<style scoped>
	.chart-container {
		width: 100%;
		float: left;
	}
	
	.el-col {
		padding: 30px 20px;
	}
</style>


