<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-select placeholder="费用类型" v-model="costType" filterable>
						<el-option v-for="item in billTypeList" :key="item" :label="item" :value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<!--<el-select placeholder="物业" v-model="departmentCode" :filterable=true>
						<el-option v-for="item in departmentList" :key="item.code" :label="item.name" :value="item.code">
						</el-option>
					</el-select>-->
					<el-select placeholder="小区" v-model="housingId" :filterable=true @change="getBuidingsById">
						<el-option v-for="item in houseList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="楼栋" v-model="buildingId" :filterable=true @change="getLocationsByBuildingId">
						<el-option v-for="item in buildList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="单元" v-model="locationId" :filterable=true @change="quyerRoomInfo">
						<el-option v-for="item in localtionList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="房屋" v-model="roomId" :filterable=true @change="queryProprietor">
						<el-option v-for="item in roomList" :key="item.id" :label="item.room" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select placeholder="业主" v-model="proprietorId" :filterable=true>
						<el-option v-for="item in proprietorList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="开始账期">
					<el-date-picker type="date" v-model="startDate" placeholder="开始账期">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="结束账期">
					<el-date-picker type="date" v-model="endDate" placeholder="结束账期">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="开始付款日期">
					<el-date-picker type="date" v-model="startPaymentDate" placeholder="选择月份">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="结束付款日期">
					<el-date-picker type="date" v-model="endPaymentDate" placeholder="选择月份">
					</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="queryBillAccount()">查询</el-button>
				</el-form-item>
				<el-form-item v-if="sysFlag">
					<el-button-group>
						<el-button type="primary" :loading="syncBtnLoading" @click="billAccountBachUpload">批量同步账单(每次200条)</el-button>
						<el-button type="primary" @click="importModelVisible=true,importLoading=false">导入待付款账单</el-button>
						<el-button type="primary" @click="exportBillAccount" :loading="exportLoding">查询条件导出账单</el-button>
						<el-button type="primary" @click="downloadBillModule">下载账单模板</el-button>
					</el-button-group>
					<el-button-group>
						<el-button type="primary" @click="entryAccount">选中账单一键收款</el-button>
						<el-button type="danger" @click="delAccount">删除选中账单</el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>
		<el-col :span="24">
			<el-tabs v-model="activeName" @tab-click="tabHandleClick">
				<el-tab-pane label="全部" name="-1"></el-tab-pane>
				<el-tab-pane label="已付款" name="1"></el-tab-pane>
				<el-tab-pane label="未付款" name="0"></el-tab-pane>
				<el-tab-pane label="已同步" name="2"></el-tab-pane>
				<el-tab-pane label="未同步" name="3"></el-tab-pane>
			</el-tabs>
			<el-table @selection-change="handleSelectionChange" :data="list" style="width: 100%" v-loading="loading" element-loading-text="拼命加载中" :summary-method="getSummaries" show-summary>
				<el-table-column type="selection" width="50">
				</el-table-column>
				<el-table-column prop="bill_entry_id" label="账单编号">
				</el-table-column>
				<el-table-column prop="payDate" label="付款日期">
				</el-table-column>
				<el-table-column prop="departmentName" label="物业">
				</el-table-column>
				<el-table-column prop="roomAddress" label="房屋编号">
				</el-table-column>
				<el-table-column prop="proprietorName" label="业主姓名">
				</el-table-column>
				<el-table-column prop="cost_type" label="费用类型">
				</el-table-column>
				<el-table-column prop="acct_period" label="账期">
				</el-table-column>
				<el-table-column prop="bill_entry_amount" label="金额">
					<template scope="scope">
						{{scope.row.bill_entry_amount}}
						<span v-if="!!scope.row.accpetMoney">(实收:{{scope.row.accpetMoney}})</span>
						<span v-if="scope.row.payStatus==1&&!scope.row.accpetMoney">(实收:{{scope.row.bill_entry_amount}})</span>
					</template>
				</el-table-column>
				<el-table-column prop="release_day" label="出账日期">
				</el-table-column>
				<el-table-column prop="deadline" label="缴费截止">
				</el-table-column>
				<el-table-column prop="payStatusString" label="付款状态">
				</el-table-column>
				<el-table-column prop="payType" label="付款方式">
				</el-table-column>
				<el-table-column prop="stateString" label="状态">
				</el-table-column>
				<el-table-column label="操作" v-if="sysFlag">
					<template scope="scope">
						<el-button v-if="scope.row.payStatus==0" type="text" size="small" @click="remindBillAccount(scope.row.id)">短信催收</el-button>
						<el-button v-if="scope.row.payStatus==0" type="text" size="small" @click="showAcceptMoneyModel(scope.row)">账单收款</el-button>
						<el-button v-if="scope.row.payStatus==0" type="text" size="small" @click="openModifyOrder(scope.row)">修改</el-button>
						<el-button v-if="scope.row.payStatus==0" type="text" @click="deleteBillAccountFromAlipay(scope.row.id)" size="small">删除</el-button>
						<el-button v-if="scope.row.payStatus==1" type="text" size="small">...</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-col>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="queryBillAccount" :current-page.sync="currPage" :page-size="pageSize" :total="count" style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog title="账单修改" v-model="midifyVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="业主姓名">
					<el-input v-model="modifyOrder.proprietorName" :readonly=true></el-input>
				</el-form-item>
				<el-form-item label="房屋号">
					<el-input v-model="modifyOrder.roomAddress" :readonly=true></el-input>
				</el-form-item>
				<el-form-item label="账单月份">
					<el-input v-model="modifyOrder.acct_period"></el-input>
				</el-form-item>
				<el-form-item label="金额">
					<el-input v-model="modifyOrder.bill_entry_amount"></el-input>
				</el-form-item>
				<el-form-item label="出账日期">
					<el-col :span="15">
						<el-input v-model="modifyOrder.release_day"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="缴费截止">
					<el-input v-model="modifyOrder.deadline"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="midifyVisible = false">取消</el-button>
				<el-button type="primary" @click="saveUpdateBillAccount" :loading="acceptLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="账单收款" v-model="acceptMoneyVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="业主姓名">
					<el-input v-model="order.proprietorName" :readonly=true></el-input>
				</el-form-item>
				<el-form-item label="账单月份">
					<el-input v-model="order.acct_period" :readonly=true></el-input>
				</el-form-item>
				<el-form-item label="金额">
					<el-input v-model="order.bill_entry_amount" :readonly=true></el-input>
				</el-form-item>
				<el-form-item label="实收款" v-if="multipleSelection.length==1">
					<el-input v-model="order.accpetMoney" type="number"></el-input>
				</el-form-item>
				<el-form-item label="房屋号">
					<el-input v-model="order.roomAddress" :readonly=true></el-input>
				</el-form-item>
				<el-form-item label="收款日期">
					<el-date-picker v-model="payDate" type="date" placeholder="选择日期" format="yyyy-MM-dd">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="付款方式">
					<el-select v-model="order.payType">
						<el-option :key="'现金'" :value="'现金'" label="现金">
							现金
						</el-option>
						<el-option :key="'支付宝'" :value="'支付宝'" label="支付宝">
							支付宝
						</el-option>
						<el-option :key="'现金'" :value="'微信'" label="微信">
							微信
						</el-option>
						<el-option :key="'POS机'" :value="'POS机'" label="POS机">
							POS机
						</el-option>
						<el-option :key="'现金'" :value="'其它'" label="其它">
							其他
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="acceptMoneyVisible = false">取消</el-button>
				<el-button type="primary" @click="savePayBillAccount" :loading="acceptLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="添加账单" v-model="addModelVisible" :close-on-click-modal="false">
			<el-form label-width="100px">
				<el-form-item label="业主姓名">
					<el-select v-model="addOrder.proprietorId" filterable placeholder="请选择">
						<el-option :key="0" :label="选择业主" :value="'业主1'">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="费用类型">
					<el-select v-model="addOrder.cost_type">
						<el-option :label="物业管理费" :key="物业管理费" :value="'物业管理费'">
						</el-option>
						<el-option :label="水电公摊" :key="水电公摊费" :value="'水电公摊费'">
						</el-option>
						<el-option :label="垃圾清运费" :key="垃圾清运费" :value="'垃圾清运费'">
						</el-option>
						<el-option :label="公共照明费" :key="公共照明费" :value="'公共照明费'">
						</el-option>
						<el-option :label="水费" :key="水电公摊费" :value="'水费'">
						</el-option>
						<el-option :label="电费" :key="电费" :value="'电费'">
						</el-option>
						<el-option :label="蒸汽费" :key="蒸汽费" :value="'蒸汽费'">
						</el-option>
						<el-option :label="其它" :key="其它" :value="'其它'"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="账期">
					<el-date-picker v-model="addOrder.acct_period" type="month" placeholder="选择日期" format="yyyy年MM月">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="出账日期">
					<el-date-picker v-model="addOrder.release_day" type="date" placeholder="选择日期" format="yyyyMMdd">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="缴费截止">
					<el-date-picker v-model="addOrder.deadline" type="date" placeholder="选择日期" format="yyyyMMdd">
					</el-date-picker>
				</el-form-item>
				<el-form-item label="账单金额">
					<el-input v-model="addOrder.bill_entry_amount" type="number"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addModelVisible = false">取消</el-button>
				<el-button type="primary" @click="saveBillAccount" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>

		<el-dialog title="导入账单" v-model="importModelVisible" :close-on-click-modal="false">
			<el-form label-width="200px">
				<el-form-item label="请选择需要导入的账单">
					<el-upload class="upload-demo" :name="excelFile" ref="upload" action="/readExcel.html" :on-success="uploadSuccess" :auto-upload="false">
						<el-button slot="trigger" size="small" type="primary">选取文件</el-button>
						<div slot="tip" class="el-upload__tip">只能上传excel文件</div>
					</el-upload>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="importModelVisible = false">取消</el-button>
				<el-button type="primary" @click="submitUpload" :loading="importLoading">开始导入</el-button>
			</div>
		</el-dialog>

		<el-dialog title="导入结果" v-model="resultVisible" :close-on-click-modal="false">
			<el-row style="text-align: center;">{{successResult}}</el-row>
			<el-row v-for="item in resultList" style="text-align: center;">{{item}}</el-row>
			<div slot="footer" class="dialog-footer">
				<el-button @click="resultVisible = false">确定</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {
		baseUtile
	} from '../../api/baseUtile';
	export default {
		data() {
			return {
				formDialogVisible: false,
				acceptMoneyVisible: false,
				orgName: '',
				companyName: '',
				list: [],
				count: 0,
				currPage: 1,
				pageSize: 10,
				loading: false,
				proprietorName: "",
				payStatus: "",
				sendStatus: "",
				housingId: "",
				buildingId: "",
				locationId: "",
				bill_entry_amount: 0,
				costType: "",
				roomId: "",
				multipleSelection: [],
				order: {
					payType: "现金",
					accpetMoney: ""
				},
				acceptLoading: false,
				addModelVisible: false,
				addOrder: {
					"proprietorId": "",
					"cost_type": "",
					"acct_period": "",
					"release_day": "",
					"deadline": "",
					"bill_entry_amount": ""
				},
				addLoading: false,
				importModelVisible: false,
				importLoading: false,
				activeName: "-1",
				proprietorId: "",
				proprietorList: [],
				houseList: [],
				buildList: [],
				localtionList: [],
				roomList: [],
				payDate: new Date(),
				modifyOrder: {},
				midifyVisible: false,
				startDate: new Date(new Date().setFullYear(2000, 0, 1)),
				endDate: new Date(new Date().setFullYear(new Date().getFullYear(), 11, 31)),
				startPaymentDate: new Date(new Date().setFullYear(2000, 0, 1)),
				endPaymentDate: new Date(new Date().setFullYear(new Date().getFullYear(), 11, 31)),
				departmentList: [],
				departmentCode: "",
				costType: "",
				syncBtnLoading: false,
				billTypeList: [],
				resultVisible: false,
				successResult: "",
				resultList: [],
				exportLoding: false,
				sysFlag: false
			}
		},
		methods: {
			downloadBillModule() {
				window.location.href = "/downloadfile/bill.xls";
			},
			exportBillAccount() {
				var that = this;
				var params = {
					startDate: baseUtile.dateFormat("yyyyMMdd", this.startDate),
					endDate: baseUtile.dateFormat("yyyyMMdd", this.endDate),
					startPaymentDate: baseUtile.dateFormat("yyyy-MM-dd", this.startPaymentDate),
					endPaymentDate: baseUtile.dateFormat("yyyy-MM-dd", this.endPaymentDate),
					proprietorName: that.proprietorName,
					payStatus: that.payStatus,
					sendStatus: that.sendStatus,
					housingId: that.housingId,
					buildingId: that.buildingId,
					locationId: that.locationId,
					roomId: that.roomId,
					costType: that.costType,
					proprietorId: that.proprietorId,
					departmentCode: that.departmentCode,
					type: "none"
				}
				this.exportLoding = true;
				baseUtile.httpSend("/exportBillAccount.html", params, data => {
					var path = data.filePath;
					this.exportLoding = false;
					path = path.substr(path.indexOf("/"), path.length);
					window.location.href = path;
				})
			},
			getBillType() {
				baseUtile.httpSend("/getBillType.html", null, data => {
					this.billTypeList = data.list;
				})
			},
			remindBillAccount(accountId) {
				var params = {
					accountId: accountId
				}
				baseUtile.httpSend("/remindBillAccount.html", params, data => {
					if(data.state == 1) {
						baseUtile.tip(data.msg, "success");
					} else {
						baseUtile.alert(data.msg + "，请联系您的服务商!")
						//						baseUtile.tip(data.msg,"error");
					}
				})
			},
			getDepartment_menu: function() {
				var that = this;
				var params = {
					currPage: 1,
					type: "none"
				}
				that.loading = true;
				baseUtile.httpSend("/department_menu.do", params, function(data) {
					that.departmentList = data.list;
				})
			},
			entryAccount: function() {
				if(this.multipleSelection.length <= 0) {
					baseUtile.tip("请选择需要收款的账单");
					return;
				}
				var owerId = this.multipleSelection[0].proprietorId;
				for(var i = 0; i < this.multipleSelection.length; i++) {
					if(owerId != this.multipleSelection[i].proprietorId) {
						baseUtile.tip("只能对同一业主进行批量收款");
						return;
					}
					if(this.multipleSelection[i].payStatus != 0) {
						baseUtile.tip("只能对未收款账单收款");
						return;
					}
				}
				this.order = JSON.parse(JSON.stringify(this.multipleSelection[0]));
				this.order.bill_entry_amount = 0;
				for(var i = 0; i < this.multipleSelection.length; i++) {
					this.order.bill_entry_amount = this.order.bill_entry_amount + this.multipleSelection[i].bill_entry_amount;
				}
				this.showAcceptMoneyModel(this.order);
			},
			delAccount: function() {
				if(this.multipleSelection.length <= 0) {
					baseUtile.tip("请选择需要删除的账单");
					return;
				}
				for(var i = 0; i < this.multipleSelection.length; i++) {
					if(this.multipleSelection[i].payStatus != 0) {
						baseUtile.tip("只能删除未付款账单！");
						return;
					}
				}
				var count = 0;
				for(var i = 0; i < this.multipleSelection.length; i++) {
					var params = {
						billAccountId: this.multipleSelection[i].id
					}
					baseUtile.httpSend("/deleteBillAccountFromAlipay.html", params, data => {
						count++;
						if(count == this.multipleSelection.length) {
							baseUtile.tip(data.msg, "success");
							this.queryBillAccount();
						}
					})
				}
			},
			queryBillAccount: function(currPage) {
				var page = 1;
				if(!!currPage) {
					this.currPage = currPage;
					page = currPage;
				}
				var that = this;
				var params = {
					startDate: baseUtile.dateFormat("yyyyMMdd", this.startDate),
					endDate: baseUtile.dateFormat("yyyyMMdd", this.endDate),
					startPaymentDate: baseUtile.dateFormat("yyyy-MM-dd", this.startPaymentDate),
					endPaymentDate: baseUtile.dateFormat("yyyy-MM-dd", this.endPaymentDate),
					currPage: page,
					proprietorName: that.proprietorName,
					payStatus: that.payStatus,
					sendStatus: that.sendStatus,
					housingId: that.housingId,
					buildingId: that.buildingId,
					locationId: that.locationId,
					roomId: that.roomId,
					costType: that.costType,
					proprietorId: that.proprietorId,
					departmentCode: that.departmentCode,
					type: "none"
				}
				that.loading = true;
				baseUtile.httpSend("/queryBillAccount.html", params, data => {
					that.list = data.list;
					that.list.forEach(function(item) {
						item.payStatus == 0 ? item.payStatusString = '未付款' : item.payStatusString = '已付款';
						switch(item.sendStatus) {
							case 0:
								item.stateString = '未同步';
								break;
							case 1:
								item.stateString = '已同步';
								break;
						}
						that.departmentList.forEach(function(tmp) {
							if(tmp.code == item.departmentCode) {
								item.department = tmp.name;
							}
						})
					})
					that.loading = false;
					that.count = data.count;
					that.bill_entry_amount = data.collect.bill_entry_amount;
				})
			},
			getSummaries: function() {
				var sums = new Array(12);
				var sumMoney = 0;
				//				this.list.forEach(function(item) {
				//					sumMoney = sumMoney + item.bill_entry_amount;
				//				})
				sums[8] = this.bill_entry_amount.toFixed(2);
				sums[0] = "合计";
				return sums;
			},
			showAcceptMoneyModel: function(order) {
				this.acceptMoneyVisible = true;
				this.order = JSON.parse(JSON.stringify(order));
			},
			savePayBillAccount: function() {
				var that = this;
				var count = 0;
				for(var i = 0; i < this.multipleSelection.length; i++) {
					var params = {
						payType: that.order.payType,
						id: this.multipleSelection[i].id,
						payDate: baseUtile.dateFormat("yyyy-MM-dd", this.payDate),
						accpetMoney: that.order.accpetMoney
					}
					that.acceptLoading = true;
					baseUtile.httpSend("/savePayBillAccount.html", params, data => {
						count++;
						if(count == that.multipleSelection.length) {
							baseUtile.tip("操作成功!", "success");
							that.acceptLoading = false;
							that.acceptMoneyVisible = false;
							that.queryBillAccount();
						}
					})
				}
				if(this.multipleSelection.length == 0) {
					var params = {
						payType: that.order.payType,
						id: that.order.id,
						payDate: baseUtile.dateFormat("yyyy-MM-dd", this.payDate)
					}
					that.acceptLoading = true;
					baseUtile.httpSend("/savePayBillAccount.html", params, data => {
						baseUtile.tip("操作成功!", "success");
						that.acceptLoading = false;
						that.acceptMoneyVisible = false;
						that.queryBillAccount();
					})
				}
			},
			saveBillAccount: function() {
				var that = this;
				if(!that.addOrder.proprietorId) {
					baseUtile.tip("请选择业主!");
					return;
				}
				if(!that.addOrder.cost_type) {
					baseUtile.tip("请选择费用类型!");
					return;
				}
				if(!that.addOrder.acct_period) {
					baseUtile.tip("请选择账期!");
					return;
				}
				if(!that.addOrder.release_day) {
					baseUtile.tip("请选择出账日期!");
					return;
				}
				if(!that.addOrder.deadline) {
					baseUtile.tip("请选择缴费截止日期!");
					return;
				}
				if(!that.addOrder.bill_entry_amount) {
					baseUtile.tip("请输入账单金额!");
					return;
				}
				that.addLoading = true;
				var params = {
					proprietorId: 1, //that.addOrder.proprietorId
					cost_type: that.addOrder.cost_type,
					acct_period: baseUtile.dateFormat("yyyyMMdd", that.addOrder.acct_period),
					release_day: baseUtile.dateFormat("yyyyMMdd", that.addOrder.release_day),
					deadline: baseUtile.dateFormat("yyyyMMdd", that.addOrder.deadline),
					bill_entry_amount: that.addOrder.bill_entry_amount
				}
				baseUtile.httpSend("/saveBillAccount.html", params, data => {
					baseUtile.tip("添加成功!", "success");
					that.addLoading = false;
					that.addModelVisible = false;
					this.queryBillAccount();
				})
			},
			quyerRoomInfo: function(locationId) {
				var that = this;
				var params = {
					locationId: locationId
				}
				baseUtile.httpSend("/quyerRoomInfo.html", params, data => {
					this.roomList = data.list;
				})
			},
			billAccountBachUpload: function() {
				var that = this;
				var params = {
					id: ""
				}
				this.syncBtnLoading = true;
				baseUtile.httpSend("/billAccountBachUpload.html", params, data => {
					this.syncBtnLoading = false;
					baseUtile.alert(data.msg);
				})
			},
			submitUpload() {
				this.importLoading = true;

				this.$refs.upload.submit();
			},
			uploadSuccess(res) {
				this.resultList = res.result;
				this.successResult = res.success;
				this.resultVisible = true;
				this.importModelVisible = false;
				this.importLoading = false;
			},
			tabHandleClick(tab) {
				this.currPage = 1;
				this.payStatus = "";
				this.sendStatus = "";
				console.log(tab.name)
				if(tab.name == 0 || tab.name == "0") {
					this.payStatus = 0;
				}
				if(tab.name == 1 || tab.name == "1") {
					this.payStatus = 1;

				}
				if(tab.name == 2 || tab.name == "2") {
					this.sendStatus = 1;

				}
				if(tab.name == 3 || tab.name == "3") {
					this.sendStatus = 0;

				}
				this.queryBillAccount();
			},
			queryHousingWy: function() {
				var that = this;
				baseUtile.httpSend("/queryHousingWy.html", null, data => {
					that.houseList = data.list;
				})
			},
			getBuidingsById: function(housingId) {
				var that = this;
				var params = {
					id: housingId
				}
				baseUtile.httpSend("/getBuidingsById.html", params, data => {
					this.buildList = data.list;
				})
			},
			getLocationsByBuildingId: function(buildingId) {
				var that = this;
				var params = {
					id: buildingId
				}
				baseUtile.httpSend("/getLocationsByBuildingId.html", params, data => {
					this.localtionList = data.list;
				})
			},
			queryProprietor: function(roomId) {
				var that = this;
				var params = {
					roomId: roomId
				}
				baseUtile.httpSend("/queryProprietor.html", params, data => {
					this.proprietorList = data.list;
				})
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			deleteBillAccountFromAlipay(billAccountId) {
				var that = this;
				baseUtile.confirm("确定删除该账单?", res => {
					if(res) {
						var params = {
							billAccountId: billAccountId
						}
						baseUtile.httpSend("/deleteBillAccountFromAlipay.html", params, data => {
							baseUtile.tip(data.msg, "success");
							that.queryBillAccount();
						})
					}
				})
			},
			openModifyOrder(order) {
				this.modifyOrder = JSON.parse(JSON.stringify(order));
				this.midifyVisible = true;
			},
			saveUpdateBillAccount() {
				this.acceptLoading = true;
				var params = {
					id: this.modifyOrder.id,
					acct_period: this.modifyOrder.acct_period,
					release_day: this.modifyOrder.release_day,
					deadline: this.modifyOrder.deadline,
					bill_entry_amount: this.modifyOrder.bill_entry_amount
				}
				baseUtile.httpSend("/saveUpdateBillAccount.html", params, data => {
					baseUtile.tip(data.msg, "success");
					this.acceptLoading = false;
					this.midifyVisible = false;
					this.queryBillAccount();
				})
			}
		},
		mounted() {
			this.queryBillAccount();
			this.queryHousingWy();
			//		this.getDepartment_menu();
			this.getBillType();
			var user = JSON.parse(sessionStorage.getItem('user'));
			if(!user.isSaler) {
				this.sysFlag = true;
			}
		}

	}
</script>


